import { Add, Cancel, Check, Home } from "@mui/icons-material";
import { Box, Button, Card, Chip, Stack, TabList, TabPanel, Tabs, Typography, Tab, Divider } from "@mui/joy";
import * as React from "react";
import { ActionModes, SettingsContext } from "../../../contexts/SettingsContext";
import { Ramo } from "../../../typings/horario";
import AddingAction from "./actions/AddingAction";
import ViewingAction from "./actions/ViewingAction";
import EditingAction from "./actions/EditingAction";

export default function ActionBar(props: { onHomeOpen: () => any }) {
	const [settings] = React.useContext(SettingsContext);

	React.useEffect(() => {
		document.getElementById("calendar-page")!.style.setProperty(
			"--calendar-bg-color",
			{
				[ActionModes.VIEWING]: "var(--primary)",
				[ActionModes.ADDING]: "var(--dark-primary)",
				[ActionModes.EDITING]: "var(--quaternary)",
			}[settings?.mode ?? ActionModes.VIEWING]
		);
	}, [settings?.mode]);

	return (
		<Box className="h-full bg-[#0008] w-[22%]">
			<Stack alignItems="center" className="relative w-full h-full pb-2">
				<Stack direction="row" className="absolute left-0 top-0 p-2 items-center justify-between w-full">
					<Button disabled={(settings?.mode ?? ActionModes.VIEWING) !== ActionModes.VIEWING} variant="solid" startDecorator={<Home />} onClick={() => props.onHomeOpen()}>
						Inicio
					</Button>
					<Typography level="title-lg" className="px-2">
						Horario USM
					</Typography>
				</Stack>
				<Box className="mt-14 px-2 w-full h-full">
					<Card className="w-full h-full relative" size="sm">
						<Stack className="w-full h-full pt-2" spacing={1}>
							<Box className="absolute top-0 left-[75%] -translate-x-1/2 -translate-y-[50%] z-10">
								<Chip size="sm" variant="outlined">
									<Typography className="px-2">
										{
											{
												[ActionModes.VIEWING]: "Visualización",
												[ActionModes.ADDING]: "Adición",
												[ActionModes.EDITING]: "Edición",
											}[settings?.mode ?? ActionModes.VIEWING]
										}
									</Typography>
								</Chip>
							</Box>
							{
								{
									[ActionModes.VIEWING]: <ViewingAction />,
									[ActionModes.ADDING]: <AddingAction />,
									[ActionModes.EDITING]: <EditingAction />,
								}[settings?.mode ?? ActionModes.VIEWING]
							}
						</Stack>
					</Card>
				</Box>
			</Stack>
		</Box>
	);
}
