import { Box, Card, CardContent, CardOverflow, Divider, Stack, Tooltip, Typography } from "@mui/joy";
import * as React from "react";
import { RamosContext } from "../../../contexts/RamosContext";
import { BLOQUE_DURATION_MINUTES } from "../../../global/constants";
import { InfoOutlined, InfoRounded } from "@mui/icons-material";
import ListData from "../../components/ListData";

export default function Resume() {
	const [ramos] = React.useContext(RamosContext);
	const [bloquesDay, setBloquesDay] = React.useState<any[]>([]);

	React.useEffect(() => {
		if (!ramos) return;
		const bloques = ramos.map(ramo => ramo.horario.map(b => ({ ...b, ramo }))).flat();
		const bloquesDay: any[] = [];
		for (let dayIndex = 0; dayIndex < 6; dayIndex++) bloquesDay.push(bloques.filter(b => b.dia === dayIndex));
		setBloquesDay(bloquesDay);
	}, [ramos]);

	const data = React.useMemo((): {
		[name: string]: {
			value: string;
			description?: string;
		};
	} => {
		const bloquesDayFiltered = bloquesDay.filter(b => b.length);
		const bloquesPerDay = bloquesDayFiltered.map(b => b.length).reduce((prev, curr) => curr + prev, 0) / bloquesDayFiltered.length;
		const hoursPerDay = (bloquesPerDay * BLOQUE_DURATION_MINUTES) / 60;

		return {
			"Bloques*/día*": {
				value: `${bloquesPerDay.toFixed(1)} (${hoursPerDay.toFixed(1)} hrs)`,
				description: `Calcula los bloques (y horas) promedio por día con clase (en este caso *${bloquesDayFiltered.length}*).`,
			},
		};
	}, [bloquesDay]);

	return (
		<Card size="sm" className="overflow-hidden">
			<CardOverflow variant="soft" sx={{ bgcolor: "black" }}>
				<CardContent orientation="horizontal" className="items-center justify-between h-full">
					<Typography level="body-sm">Estadísticas</Typography>
				</CardContent>
				<Divider inset="context" />
			</CardOverflow>
			{ramos?.length ? <ListData data={data} /> : <Typography level="body-sm">No hay ramos aún</Typography>}
		</Card>
	);
}
