import { Box, Button, Card, IconButton, List, Stack, Typography } from "@mui/joy";
import * as React from "react";
import { ActionModes, SettingsContext } from "../../../../contexts/SettingsContext";
import { ArrowBack, Delete } from "@mui/icons-material";
import { RamosContext } from "../../../../contexts/RamosContext";
import { ParaleloOption, RamoOption } from "./AddingAction";
import { ASIGNATURAS } from "../../../../global/data";
import { Ramo } from "../../../../typings/horario";

export default function EditingAction() {
	const [settings, setSettings] = React.useContext(SettingsContext);
	const [ramos, setRamos] = React.useContext(RamosContext);

	const ramo = React.useMemo(() => ramos![0], []);
	React.useEffect(() => {
		setRamos!(r => [...r.map(r => (r.sigla === ramo.sigla ? { ...r, highlighted: true } : r))]);
	}, []);

	const paralelosOptions = React.useMemo(() => Object.keys(ASIGNATURAS[settings?.sede ?? ""][settings?.jornada ?? ""][settings?.periodo ?? ""][ramo.sigla] ?? {}), [ramo, settings]);

	return (
		<Stack spacing={1} className="w-full h-full">
			<Button
				startDecorator={<ArrowBack />}
				variant="outlined"
				onClick={() => {
					setRamos!(r => [...r.map(r => ({ ...r, highlighted: false }))]);
					setSettings!(s => ({ ...s, mode: ActionModes.VIEWING }));
				}}
			>
				Volver
			</Button>
			<Stack className="w-full h-full" spacing={2} direction="column" justifyContent="space-between" alignItems="center">
				<Stack className="w-full h-full" direction="column" spacing={1}>
					<RamoOption ramo={ramo} />
					{/* <Card //
						size="sm"
						variant="outlined"
						className="h-full w-full"
					>
						<Box className="gap-1 absolute top-0 left-0 bottom-0 right-0 overflow-auto p-2">
							<List className="gap-1 absolute top-0 left-0 bottom-0 right-0 overflow-auto">
								{paralelosOptions.map(p => {
									const paralelosRamo = ASIGNATURAS[settings!.sede][settings!.jornada][settings!.periodo][ramo.sigla];
									const ramoParalelo = paralelosRamo[p];

									return (
										<ParaleloOption //
											selected={ramo.paralelo == p}
											onClick={() => {
												setRamos!(r => [...r.map(r => (r.sigla === ramo.sigla ? ramoParalelo : r))]);
											}}
											onMouseEnter={() => {
												setRamos!(r => [...r.filter(r => r.sigla !== ramo.sigla), { ...ramoParalelo, highlighted: true }]);
											}}
											onMouseLeave={() => {
												setRamos!(r => [...r.filter(r => r.sigla !== ramo.sigla), ramo]);
											}}
											key={p}
											ramo={ramoParalelo}
										/>
									);
								})}
							</List>
						</Box>
					</Card> */}
				</Stack>
				<Stack justifyContent={"space-between"} direction="row-reverse" alignItems="center" className="w-full">
					<Button
						color="danger"
						onClick={() => {
							if (!window.confirm(`¿Seguro que quieres borrar ${ramo.sigla} de tu horario?`)) return;
							setRamos!(r => [...r.filter(r => r.sigla !== ramo.sigla)]);
							setSettings!(s => ({ ...s, mode: ActionModes.VIEWING }));
						}}
						startDecorator={<Delete />}
					>
						Eliminar {ramo.sigla}
					</Button>
				</Stack>
			</Stack>
		</Stack>
	);
}
