import stc from "string-to-color";
import { SIGLA_COLORS, NOMBRE_COLORS, BLOQUE_COMIDA, BLOQUE_COMIDA_DURATION_MINUTES, BLOQUE_DURATION_MINUTES, BREAK_DURATION_MINUTES, DAY_FIRST_MINUTES } from "../global/constants";
import { Ramo, RamoCarrera, RamoPrograma } from "../typings/horario";
import Time from "./time";
import { CARRERAS, PROGRAMAS } from "../global/data";

export default class Utils {
	static ramoColor(sigla: string, nombre?: string, color?: string) {
		nombre = nombre?.toUpperCase();
		sigla = sigla.toUpperCase();

		let col = color ?? SIGLA_COLORS[sigla] ?? SIGLA_COLORS[sigla.slice(0, 3)];
		try {
			if (!col || nombre) {
				nombre = nombre?.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
				if (nombre)
					for (const [str, hex] of Object.entries(NOMBRE_COLORS)) {
						if (nombre.match(str)) {
							col = hex;
							break;
						}
					}
			}
		} catch {}
		col ??= stc(sigla.slice(0, 3));
		return col;
	}

	static bloqueToHora(bloque: number) {
		return Time.MinutesToHHMM(DAY_FIRST_MINUTES + BLOQUE_DURATION_MINUTES * (bloque - 1) + BREAK_DURATION_MINUTES * Math.floor((bloque - 1) / 2) + (bloque > BLOQUE_COMIDA ? BLOQUE_COMIDA_DURATION_MINUTES : 0));
	}

	static openInNewTab(url: string) {
		const newWindow = window.open(url, "_blank", "noopener,noreferrer");
		if (newWindow) newWindow.opener = null;
	}

	static getRamoData(ramo: Ramo, sede?: string): [RamoCarrera | null, RamoPrograma | null] {
		let ramoCarrera: RamoCarrera | null = null;
		try {
			CARRERAS.some(c =>
				Object.values(c["menciones/especialidades"]) //
					.some(m =>
						Object.values(m.planes) //
							.some(p => p.malla.some(s => Object.entries(s).some(([sigla, ramo_carrera]) => sigla === ramo.sigla && (ramoCarrera = ramo_carrera))))
					)
			);
		} catch (err) {}

		let ramoPrograma: RamoPrograma | null = null;
		try {
			if (sede) {
				if (!(sede in PROGRAMAS)) sede = "Campus " + sede;
				if (sede in PROGRAMAS) {
					Object.entries(PROGRAMAS[sede][ramo.departamento]).some(
						(
							[tipo, programa] //
						) => Object.entries(programa).some(([sigla, ramo_programa]) => sigla === ramo.sigla && (ramoPrograma = { ...ramo_programa, creditos: +ramo_programa.creditos, sigla, tipo: tipo as any }))
					);
				}
			}
		} catch (err) {}

		return [ramoCarrera, ramoPrograma];
	}
}
