import { Box, Card, Chip, Divider, Sheet, Stack, Typography } from "@mui/joy";
import * as React from "react";
import { RamosContext } from "../../../contexts/RamosContext";
import { Bloque as TypeBloque } from "../../../typings/horario";
import Item from "./Item";
import { BloquesContext } from "../../../contexts/BloquesContext";

export default function Cell(props: { bloqueIndex: number; dayIndex: number }) {
	const [ramos] = React.useContext(RamosContext);
	const [bloquesI, setBloquesI] = React.useContext(BloquesContext);

	const current = React.useMemo(() => {
		if (!ramos) return [];

		let b: TypeBloque[] = [];
		for (let i = 0; i < ramos.length; i++) {
			const r = ramos[i];
			b.push(
				...r.horario //
					.filter(b => b.bloque === props.bloqueIndex + 1 && b.dia === props.dayIndex)
					.map(b => ({ ...b, ramo: r }))
			);
		}

		return [...b, ...(bloquesI ?? [])];
	}, [ramos]);

	if (!current?.length) return <BloqueBox vacio></BloqueBox>;

	return (
		<BloqueBox>
			{{
				1: (
					<Stack justifyContent="space-between" direction="row" className="w-full" alignItems="start" spacing={0}>
						<Item current={current[0] as any} solo />
					</Stack>
				),
				2: (
					<Stack justifyContent="space-between" direction="row" className="w-full" alignItems="start" spacing={-3}>
						<Box className="w-full">
							<Item current={current[0] as any} />
						</Box>
						<Box className="w-full">
							<Item current={current[1] as any} right />
						</Box>
					</Stack>
				),
			}[current.length] ?? (
				<Stack className="w-full">
					{/* {current.map(r => (
						<Item current={r as any} compact />
					))} */}
					TOPE 3+
				</Stack>
			)}
		</BloqueBox>
	);
}

function BloqueBox(props: { children?: any; vacio?: boolean }) {
	return (
		<Sheet {...(props.vacio ? { sx: { background: "#000a" } } : {})} className={`h-full w-full p-2 overflow-hidden`}>
			<Stack justifyContent="start" alignItems="start" className="h-full w-full" spacing={-1}>
				{props?.children}
			</Stack>
		</Sheet>
	);
}
