import { Check, Cancel, InfoOutlined, Toll, FindReplace, Warning, Schedule } from "@mui/icons-material";
import { TabList, Chip, TabPanel, Button, Divider, Tabs, Tab, tabClasses, Typography, Tooltip, Stack, Input, Autocomplete, Box, Card, List, ListItem, ListItemButton, Sheet, AutocompleteOption, ListItemContent, ListItemDecorator, CardOverflow, Checkbox, FormControl, FormHelperText } from "@mui/joy";
import * as React from "react";
import { SettingsContext, ActionModes } from "../../../../contexts/SettingsContext";
import { Bloque, BloqueItem, Ramo, RamoCarrera, RamoPrograma } from "../../../../typings/horario";
import { RamosContext } from "../../../../contexts/RamosContext";
import _ from "lodash";
import { ListItemText } from "@mui/material";
import { ASIGNATURAS, CARRERAS, PROGRAMAS } from "../../../../global/data";
import Utils from "../../../../utils/utils";
import Color from "color";
import SCT from "../SCT";
import { MallaContext } from "../../../../contexts/MallaContext";

export default function AddingAction() {
	const [_, setSettings] = React.useContext(SettingsContext);
	const [ramos, setRamos] = React.useContext(RamosContext);

	const [selected, setSelected] = React.useState<Ramo | Bloque | null>(null);
	let replaces = selected && ramos?.some(r => !r.highlighted && r.sigla == (selected as Ramo)?.sigla);

	return (
		<Stack spacing={1} className="w-full h-full">
			<Button
				{...(replaces ? { color: "warning" } : { color: "primary" })}
				disabled={!selected}
				startDecorator={replaces ? <FindReplace /> : <Check />}
				onClick={() => {
					if ("paralelo" in selected!)
						setRamos!(r => {
							// let rc: RamoCarrera | null = null;
							// CARRERAS.some(c =>
							// 	Object.values(c["menciones/especialidades"]) //
							// 		.some(m =>
							// 			Object.values(m.planes) //
							// 				.some(p => p.malla.some(s => Object.entries(s).some(([sigla, ramo_carrera]) => sigla === selected.sigla && (rc = ramo_carrera))))
							// 		)
							// );
							const _r = [selected as Ramo];
							_r.forEach(r =>
								r.horario.forEach((b: Bloque, i: number, arr: Bloque[]) => {
									let j = i;
									if (!("relativeIndex" in b))
										while (arr[j] && arr[j].bloque === b.bloque + (j - i) && arr[j].sala === b.sala) {
											Object.assign(b, {
												relativeIndex: j - i,
											} as BloqueItem);
											j++;
										}
								})
							);

							return [...r.filter(r => !r.highlighted && !_r.some(rr => rr.sigla === r.sigla)), ..._r];
						});

					setSettings!(s => ({ ...s, mode: ActionModes.VIEWING }));
				}}
			>
				{replaces ? "Reemplazar" : "Aceptar"}
			</Button>
			<Button
				startDecorator={<Cancel />}
				color="danger"
				variant="outlined"
				onClick={() => {
					setSettings!(s => ({ ...s, mode: ActionModes.VIEWING }));
				}}
			>
				Cancelar
			</Button>
			<Divider>¿Qué vas a añadir?</Divider>
			<Tabs className="h-full w-full" defaultValue={-1} sx={{ bgcolor: "transparent" }} size="sm">
				<TabList
					disableUnderline
					sx={{
						p: 0.5,
						gap: 0.5,
						borderRadius: "xl",
						border: "1px solid white",
						bgcolor: "background.level1",
						justifyContent: "space-between",
						[`& .${tabClasses.root}[aria-selected="true"]`]: {
							boxShadow: "sm",
							bgcolor: "background.surface",
						},
						[`& .${tabClasses.root}`]: {
							width: "100%",
							[":hover"]: {
								bgcolor: "background.level2",
							},
						},
					}}
				>
					<Tab disableIndicator variant="plain" color="neutral">
						<Typography
							endDecorator={
								<Tooltip enterDelay={500} enterNextDelay={500} variant="outlined" title="Añadir todo un ramo oficial con sus respectivos horarios.">
									<InfoOutlined fontSize="small" className="opacity-50" />
								</Tooltip>
							}
						>
							Ramo
						</Typography>
					</Tab>
					<Tab disableIndicator variant="plain" color="neutral">
						<Typography
							endDecorator={
								<Tooltip enterDelay={500} enterNextDelay={500} variant="outlined" title="Añadir un bloque individual. Útil para mayor flexibilidad a la hora de personalizar un horario.">
									<InfoOutlined fontSize="small" className="opacity-50" />
								</Tooltip>
							}
						>
							Bloque
						</Typography>
					</Tab>
				</TabList>
				<TabPanel value={-1}></TabPanel>
				<TabPanel value={0}>
					<RamoPanel onSelected={value => setSelected(value)} />
				</TabPanel>
				<TabPanel value={1}>
					<BloquePanel onSelected={value => setSelected(value)} />
				</TabPanel>
			</Tabs>
		</Stack>
	);
}

function BloquePanel(props: { onSelected: (value: Ramo | null) => any }) {
	return (
		<Stack spacing={1}>
			<Divider>Entrega detalles del bloque</Divider>
			<Typography color="warning" startDecorator={<Warning />}>
				Aún no está implementado!
			</Typography>
		</Stack>
	);
}

function RamoPanel(props: { onSelected: (value: Ramo | null) => any }) {
	const [settings] = React.useContext(SettingsContext);
	const [ramos, setRamos] = React.useContext(RamosContext);
	const [malla] = React.useContext(MallaContext);

	const [sigla, setSigla] = React.useState("");
	const [paralelo, setParalelo] = React.useState<Ramo | null>(null);

	const [highlighted, setHighlighted] = React.useState<Ramo | null>(null);
	const [filterMalla, setFilterMalla] = React.useState(localStorage.getItem("horario-use-malla")?.charAt(0) === "1");
	const enabledFilter = React.useMemo(() => malla && (malla.semestres?.length ?? 0) > 0 && malla.semestres!.some(semestre => semestre.some(ramo => ramo.locked)), [malla?.semestres]);

	React.useEffect(() => {
		if (highlighted) setRamos?.(r => [...r, { ...highlighted, highlighted: true }]);
		else setRamos?.(r => r.filter(r => !r.highlighted));

		return () => setRamos?.(r => r.filter(r => !r.highlighted));
	}, [highlighted]);
	React.useEffect(() => {
		setParalelo(null);
		setHighlighted(null);
	}, [sigla]);
	React.useEffect(() => {
		if (paralelo) setHighlighted(paralelo);
		props.onSelected(paralelo);
	}, [paralelo]);

	const ramosOptions = React.useMemo(() => {
		const siglas =
			filterMalla && malla?.semestres
				? malla.semestres
						.flat()
						.filter(ramo => !ramo.locked && !ramo.checked)
						.map(r => r.sigla)
				: [];
		return Object.entries(ASIGNATURAS[settings?.sede ?? ""][settings?.jornada ?? ""][settings?.periodo ?? ""] ?? {}) //
			.filter(([sigla, ramo]) => sigla !== "semestre" && (filterMalla && siglas.length ? siglas.includes(sigla) : true))
			.map(([sigla, ramo]) => `${sigla} ${Object.values(ramo)[0].nombre}`.normalize("NFD").replace(/[\u0300-\u036f]/g, ""));
	}, [settings, filterMalla]);
	const paralelosOptions = React.useMemo(() => Object.keys(ASIGNATURAS[settings?.sede ?? ""][settings?.jornada ?? ""][settings?.periodo ?? ""][sigla] ?? {}), [sigla]);

	return (
		<Stack spacing={1} className="w-full h-full">
			<Divider>¿Qué ramo vas a añadir?</Divider>
			<Stack direction="column" spacing={1}>
				<Stack width="100%" direction="row" justifyContent="space-between" alignItems="center">
					<Checkbox
						disabled={!enabledFilter}
						checked={filterMalla}
						onChange={ev => {
							const value = ev.target.checked;
							setFilterMalla(value);
							localStorage.setItem("horario-use-malla", value ? "1" : "0");
						}}
						label={"Filtrar de malla interactiva"}
					/>
					<Tooltip variant="outlined" title={<Box maxWidth={300}>Filtra solo los ramos disponibles (desbloqueados) de la pestaña de malla interactiva. Necesita tener al menos un ramo marcado.</Box>}>
						<InfoOutlined />
					</Tooltip>
				</Stack>
				<Autocomplete //
					placeholder="Introduce el ramo"
					options={ramosOptions}
					onChange={(_, v) => v && setSigla(curr => v.split(" ")[0] ?? curr)}
					openOnFocus
					autoComplete
					value={sigla}
					autoFocus
					disableClearable
					filterSelectedOptions
					noOptionsText="No hay resultados"
					filterOptions={(options, state) =>
						options.filter(op =>
							state.inputValue
								.toUpperCase()
								.normalize("NFD")
								.replace(/[\u0300-\u036f]/g, "")
								.split(" ")
								.map(s => op.includes(s))
								.every(s => s)
						)
					}
					renderOption={(props, option) => {
						const [sigla, ...nombre] = option.split(" ");
						let replaces = ramos?.some(r => !r.highlighted && r.sigla == sigla);

						return (
							<AutocompleteOption {...props}>
								<ListItemContent sx={{ fontSize: "sm" }}>
									<Typography color={replaces ? "warning" : "primary"}>
										{sigla}
										{replaces ? (
											<Typography level="body-xs" className="opacity-50">
												{" "}
												(EXISTE EN HORARIO)
											</Typography>
										) : (
											<></>
										)}
									</Typography>
									<Typography level="body-xs">{nombre.join(" ")} </Typography>
								</ListItemContent>
							</AutocompleteOption>
						);
					}}
				/>
			</Stack>
			{sigla && (
				<>
					<RamoOption ramo={Object.values(ASIGNATURAS[settings?.sede ?? ""]?.[settings?.jornada ?? ""]?.[settings?.periodo ?? ""]?.[sigla])?.[0]} />
					<Divider>¿Qué paralelo?</Divider>
					<Card //
						size="sm"
						variant="outlined"
						className="h-full w-full"
					>
						<Box className="gap-1 absolute top-0 left-0 bottom-0 right-0 overflow-auto p-2">
							<List className="gap-1 absolute top-0 left-0 bottom-0 right-0 overflow-auto">
								{paralelosOptions.map(p => {
									const paralelosRamo = ASIGNATURAS[settings!.sede][settings!.jornada][settings!.periodo][sigla];
									return (
										<ParaleloOption //
											selected={paralelo?.paralelo == p}
											onClick={() => setParalelo(paralelosRamo[p])}
											onMouseEnter={() => setHighlighted(paralelosRamo[p])}
											onMouseLeave={() => setHighlighted(paralelo ?? null)}
											key={p}
											ramo={paralelosRamo[p]}
										/>
									);
								})}
							</List>
						</Box>
					</Card>
				</>
			)}
		</Stack>
	);
}

export function RamoOption(props: { ramo: Ramo }) {
	const [settings] = React.useContext(SettingsContext);

	const { ramo } = props;
	React.useEffect(() => {
		ramo.horario = _.uniqBy(ramo.horario, bloque => bloque.bloque + bloque.dia);
	}, [ramo]);

	const [ramoCarrera, ramoPrograma] = React.useMemo(() => Utils.getRamoData(ramo, settings?.sede), [ramo, settings?.sede]);

	const color = Color(Utils.ramoColor(ramo.sigla, ramo.nombre, ramo.color));

	const elements = React.useMemo(() => {
		const elements: React.ReactNode[] = [];
		if (ramoPrograma)
			elements.push(
				<Stack spacing={1}>
					<Typography level="body-xs" className="uppercase">
						RAMO{" "}
						<Tooltip
							variant="outlined"
							title={
								<Box maxWidth={200}>
									<Typography>
										{
											{
												AMBOS: "Equivale a un ramo que se puede de Marzo a Julio, o bien de Julio a Diciembre.",
												IMPAR: "Equivale a un ramo que se puede dar en el periodo desde Marzo a Julio.",
												PAR: "Equivale a un ramo que se puede dar en el periodo desde Julio a Diciembre.",
												ELECTIVO: "Los electivos se encuentran sujetos a un cupo mínimo.",
											}[ramoPrograma.tipo]
										}
									</Typography>
								</Box>
							}
						>
							<Typography color="warning" endDecorator={<InfoOutlined />}>
								{ramoPrograma.tipo.replace("AMBOS", "PAR E IMPAR")}
							</Typography>
						</Tooltip>{" "}
						DE {ramo.departamento}
					</Typography>
				</Stack>
			);
		if (ramoCarrera)
			elements.push(
				<Stack spacing={-0.5}>
					{Object.entries(ramoCarrera.horas).map(([key, hora]) => (
						<>
							<Typography key={key} level="body-sm" className={`${!hora ? "opacity-50" : ""}`}>
								{key.toUpperCase()}: <Typography textColor={color.lighten(0.2).hex()}>{hora} HRS.</Typography>
							</Typography>
						</>
					))}
				</Stack>
			);
		elements.push(
			<Button
				disabled={!ramoPrograma?.programa}
				size="sm"
				variant={ramoPrograma?.programa ? "soft" : "outlined"}
				sx={{
					backgroundColor: color.darken(0.4).saturate(0.2).hex(),
					["&:hover"]: { backgroundColor: color.darken(0.25).hex() },
					["&:active"]: { backgroundColor: color.darken(0.1).desaturate(0.2).hex() },
				}}
				onClick={() => ramoPrograma?.programa && Utils.openInNewTab(ramoPrograma.programa)}
				endDecorator={<Schedule color="info" />}
			>
				{ramoPrograma?.programa ? "Ver programa" : "No se encontró programa"}
			</Button>
		);
		return elements;
	}, [ramoPrograma, ramoCarrera, ramo]);

	return (
		<Card
			size="sm"
			variant="outlined"
			sx={{
				background: color.darken(0.7).saturate(0.7).hex(),
			}}
		>
			{ramoCarrera || ramoPrograma ? (
				<Box className="absolute top-0 right-0 m-2 z-[2]">
					<SCT sigla={ramo.sigla} creditos={ramoCarrera?.creditos ?? ramoPrograma?.creditos ?? 0} />
				</Box>
			) : (
				<></>
			)}
			<Typography level="title-lg">{ramo.sigla}</Typography>
			<Typography level="body-sm" sx={{ color: "white" }}>
				{ramo.nombre}
			</Typography>
			{elements.length > 0 ? (
				<>
					<Divider />
					{elements}
				</>
			) : (
				<></>
			)}
		</Card>
	);
}

export function ParaleloOption(props: {
	//
	ramo: Ramo;
	onClick: () => any;
	onMouseEnter: () => any;
	onMouseLeave: () => any;
	selected: boolean;
}) {
	const { ramo } = props;

	const [ramos] = React.useContext(RamosContext);
	const isColliding = React.useMemo(
		() =>
			ramos
				?.filter(r => !r.highlighted && r.sigla !== ramo.sigla)
				.filter(r => ramos.some(rr => rr.sigla === r.sigla && rr.paralelo === r.paralelo))
				.flatMap(r => r.horario)
				.some(br => ramo.horario.some(bh => br.bloque === bh.bloque && br.dia === bh.dia)),
		[ramos]
	);

	let disabled = ramos?.some(rr => !rr.highlighted && rr.sigla === ramo.sigla && rr.paralelo === ramo.paralelo);

	return (
		<Tooltip
			color="warning"
			variant="outlined"
			disableInteractive
			title={
				isColliding ? (
					<Typography level="body-sm" color="neutral">
						El paralelo genera <Typography color="warning">tope</Typography> con el horario actual
					</Typography>
				) : (
					""
				)
			}
		>
			<ListItem
				variant="outlined" //
				onClick={() => !disabled && props.onClick()}
				onMouseEnter={() => !disabled && props.onMouseEnter()}
				onMouseLeave={() => !disabled && props.onMouseLeave()}
			>
				<ListItemButton variant="solid" sx={{ background: "black" }} className={`justify-between flex flex-row gap-2 align-center overflow-hidden ${!disabled && isColliding ? "warning-bg" : ""}`} disabled={disabled} selected={props.selected}>
					{!disabled || <div className="absolute left-0 top-0 w-full h-full bg-[#0008] z-10" />}
					<Typography className="origin-top scale-[2.7] tracking-tighter -translate-y-5 font-bold translate-x-1">{ramo.paralelo}</Typography>
					<Stack className="w-[70%]" direction="column" spacing={1}>
						{ramo.profesor.map(profesor => (
							<Typography className="text-right line-clamp-1 truncate" color={isColliding ? "warning" : "neutral"}>
								{profesor}
							</Typography>
						))}
					</Stack>
				</ListItemButton>
			</ListItem>
		</Tooltip>
	);
}
