import { Box, Card, FormControl, FormHelperText, FormLabel, Option, Select, Switch, Tooltip, Typography } from "@mui/joy";
import { switchClasses } from "@mui/joy/Switch";
import * as React from "react";
import { DarkMode, Lock, Warning, WbSunny } from "@mui/icons-material";
import { SettingsContext } from "../../../contexts/SettingsContext";

import "./SedeLocator.css";

import IMAGE_CAMPUS_SAN_JOAQUIN from "../../../static/campus_san_joaquin.jpg";
import IMAGE_CAMPUS_CASA_CENTRAL from "../../../static/campus_casa_central.jpg";
import IMAGE_CAMPUS_VITACURA from "../../../static/campus_vitacura.jpg";
import IMAGE_CAMPUS_CONCEPCION from "../../../static/campus_concepcion.jpg";
import IMAGE_CAMPUS_VIÑA_DEL_MAR from "../../../static/campus_viña_del_mar.jpg";
import IMAGE_STARS_BANNER from "../../../static/stars_seamless_patt.jpg";
import IMAGE_SUN_BANNER from "../../../static/sun_banner.jpg";
import { RamosContext } from "../../../contexts/RamosContext";
import { ASIGNATURAS } from "../../../global/data";

export default function SedeLocator() {
	const [settings, setSettings] = React.useContext(SettingsContext);
	const [ramos] = React.useContext(RamosContext);

	let vespertina = React.useMemo(() => settings?.jornada === "Vespertina", [settings?.jornada]);
	let [blockedJornada, setBlockedJornada] = React.useState(true);
	const sedeOptions = React.useMemo(() => Object.keys(ASIGNATURAS).filter(k => k !== "date"), []);
	const sedeImage = React.useMemo(
		() =>
			({
				"Santiago San Joaquín": <img src={IMAGE_CAMPUS_SAN_JOAQUIN} />,
				"Casa Central Valparaíso": <img src={IMAGE_CAMPUS_CASA_CENTRAL} />,
				"Santiago Vitacura": <img src={IMAGE_CAMPUS_VITACURA} />,
				Concepción: <img src={IMAGE_CAMPUS_CONCEPCION} />,
				"Santiago (Vesp. JMC)": <img src={IMAGE_CAMPUS_VIÑA_DEL_MAR} />,
				"Viña del Mar": <img src={IMAGE_CAMPUS_VIÑA_DEL_MAR} />,
			}[settings?.sede ?? ""]),
		[settings?.sede]
	);

	const [periodoOptions, setPeriodoOptions] = React.useState<string[]>([]);

	React.useEffect(() => {
		const periodos = Object.entries(settings?.sede && settings?.jornada ? ASIGNATURAS[settings.sede][settings.jornada] : {})
			.filter(([periodo, ramos]) => Object.keys(ramos).length > 0)
			.map(([periodo, ramos]) => periodo);

		setPeriodoOptions(periodos);
		setSettings?.(s => ({ ...s, periodo: periodos[0] ?? "" }));
	}, [setSettings, settings?.sede, settings?.jornada]);

	return (
		<Card {...(!settings?.sede ? { variant: "soft", color: "danger" } : {})} orientation="horizontal" id="sede-selector" className={`flex justify-end items-center overflow-hidden relative ${!settings?.sede ? "error" : ""}`}>
			{!ramos?.length || (
				<div className="absolute left-0 top-0 w-full h-full z-10 bg-[#000a] backdrop-blur-sm grid items-center justify-center">
					<Typography startDecorator={<Warning />}>No se puede modificar cuando hay ramos ya inscritos</Typography>
				</div>
			)}

			<link rel="preload" as="image" href={IMAGE_CAMPUS_SAN_JOAQUIN} />
			<link rel="preload" as="image" href={IMAGE_CAMPUS_CASA_CENTRAL} />
			<link rel="preload" as="image" href={IMAGE_CAMPUS_VITACURA} />
			<link rel="preload" as="image" href={IMAGE_CAMPUS_CONCEPCION} />
			<link rel="preload" as="image" href={IMAGE_CAMPUS_VIÑA_DEL_MAR} />
			<link rel="preload" as="image" href={IMAGE_STARS_BANNER} />
			<link rel="preload" as="image" href={IMAGE_SUN_BANNER} />

			<div hidden={!settings?.sede} id="sede-locator-bg-overlay" key={`jornada-${vespertina ? "vespertina" : "diurna"}`} className={`jornada-${vespertina ? "vespertina" : "diurna"}`} />
			<div id="sede-locator-bg" key={settings?.sede ?? ""}>
				{sedeImage ?? <div className="no-sede" />}
			</div>

			{settings?.periodo && (
				<Box className="absolute left-0 -bottom-6 m-2 px-1">
					<Typography
						key={`${settings?.sede + settings?.jornada}`}
						sx={{
							textShadow: "0 2px 5px black, 0 2px 10px black",
						}}
						className="mix-blend-difference drop-shadow-sm animate-slide-up-fade animate-duration-200"
						level="h1"
						fontSize={90}
					>
						{settings?.periodo}
					</Typography>
				</Box>
			)}

			<div
				style={{
					background: vespertina ? "#0003" : "#fff1",
				}}
				className="flex items-center justify-between glass-bg px-8 py-5 rounded-lg gap-10 z-[1]"
			>
				{settings?.sede ? (
					<FormControl className="flex gap-2">
						<div className="flex flex-col">
							<FormLabel sx={{ mb: 0 }}>
								Jornada{" "}
								{blockedJornada ? (
									<Tooltip variant="outlined" title={`Se detectaron únicamente horarios para carreras ${vespertina ? "vespertinas" : "diurnas"}.`}>
										<Lock sx={{ transform: "scale(0.7)" }} fontSize="small" />
									</Tooltip>
								) : (
									<></>
								)}
							</FormLabel>
							<FormHelperText sx={{ mt: 0 }}>{vespertina ? "Vespertina" : "Diurna"}</FormHelperText>
						</div>
						<Switch //
							disabled={blockedJornada}
							color="neutral"
							size="lg"
							startDecorator={<WbSunny {...(!vespertina ? { style: { filter: "drop-shadow(0 0 0.3rem white)" } } : { opacity: 0.3 })} />}
							endDecorator={<DarkMode {...(vespertina ? { style: { filter: "drop-shadow(0 0 0.3rem white)" } } : { opacity: 0.3 })} />}
							onChange={e => setSettings?.(s => ({ ...s, jornada: e.target.checked ? "Vespertina" : "Diurna" }))}
							checked={vespertina}
						/>
					</FormControl>
				) : (
					<></>
				)}
				<FormControl required className="min-w-16" error={!settings?.sede}>
					<FormLabel>Sede / Campus</FormLabel>
					<Select //
						variant="outlined"
						placeholder="Selecciona sede/campus"
						value={settings?.sede ?? ""}
						onChange={(_, sede) => {
							if (!sede) return;
							setSettings?.(s => ({ ...s, sede: sede }));
							const jornadas = ASIGNATURAS[sede];
							const someJornadaEmpty = Object.values(jornadas).filter(periodos => Object.values(periodos).some(ramos => Object.values(ramos).length > 0)).length <= 1;
							setBlockedJornada(someJornadaEmpty);
							if (someJornadaEmpty)
								setSettings?.(s => ({
									...s,
									jornada:
										Object.entries(jornadas) //
											.find(
												(
													[jornada, periodos] //
												) => Object.values(periodos).some(ramos => Object.values(ramos).length > 0)
											)?.[0] ?? "",
								}));
						}}
					>
						{sedeOptions.map((s, i) => (
							<Option key={i} value={s}>
								{s}
							</Option>
						))}
					</Select>
				</FormControl>
			</div>
		</Card>
	);
}
