import { InfoOutlined } from "@mui/icons-material";
import { Stack, Box, Tooltip, Typography } from "@mui/joy";
import * as React from "react";

export interface ListDataProps {
	data: {
		[key: string]: {
			value: string;
			description?: string;
		};
	};
}

const parse = (text: string) => {
	const offsets: {
		[index: number]: {
			replacement: React.ReactNode;
			skip: number;
		};
	} = {};

	text.replace(/\*(.*?)\*/g, (match, p1, offset) => {
		offsets[offset] = {
			replacement: <Typography color="primary">{p1}</Typography>,
			skip: match.length,
		};
		return "";
	});

	let i = 0;
	return (
		<>
			{text.split("").map((_, __, arr) => {
				if (i > arr.length - 1) return "";
				const offset = offsets[i];
				if (offset?.skip) i += offset.skip;
				else i += 1;
				return offset?.replacement ?? arr[i - 1];
			})}
		</>
	);
};

export default function ListData({ data }: ListDataProps) {
	return (
		<Stack direction="column" spacing={1}>
			{Object.entries(data).map(([key, object]) => {
				return (
					<Stack spacing={10} key={key} direction="row" justifyContent="space-between" alignItems="center">
						<Typography level="title-sm">
							{parse(key)}
							{object.description ? (
								<Box component="span" className="px-1">
									<Tooltip title={<Box maxWidth={300}>{parse(object.description)}</Box>} variant="outlined">
										<InfoOutlined />
									</Tooltip>
								</Box>
							) : (
								<></>
							)}
							:
						</Typography>
						<Typography level="body-sm">{parse(object.value)}</Typography>
					</Stack>
				);
			})}
		</Stack>
	);
}
