import _ASIGNATURAS from "../static/horario_asignaturas.json";
import _CARRERAS from "../static/planes_carreras.json";
import _PROGRAMAS from "../static/programas_academicos.json";
import { Carrera, Ramo } from "../typings/horario";

//@ts-ignore
let ASIGNATURAS: {
	[sede: string]: {
		[jornada: string]: {
			[periodo: string]: {
				[sigla_ramo: string]: {
					[paralelo: string]: Ramo;
				};
			};
		};
	};
} = _ASIGNATURAS;

interface RamoProgramas {
	[sigla_ramo: string]: {
		nombre: string;
		creditos: string;
		programa: string;
	};
}
//@ts-ignore
let PROGRAMAS: {
	[sede: string]: {
		[departamento: string]: {
			PAR: RamoProgramas;
			IMPAR: RamoProgramas;
			AMBOS: RamoProgramas;
			ELECTIVO: RamoProgramas;
		};
	};
} = _PROGRAMAS;
let CARRERAS = _CARRERAS as Carrera[];
export { ASIGNATURAS, CARRERAS, PROGRAMAS };
