import { InfoOutlined } from "@mui/icons-material";
import HORARIO_ASIGNATURAS from "../../../static/horario_asignaturas.json";

import { Box, Card, Tooltip, Typography } from "@mui/joy";
import dayjs from "dayjs";
import * as React from "react";

export default function UpdatedBadge() {
	const updated = React.useMemo(() => dayjs(HORARIO_ASIGNATURAS.date * 1000), []);
	return (
		<Box component="div" alignItems="center" className="w-auto text-right right-0 pointer-events-auto z-10">
			<Typography level="body-xs" fontSize={14} className="px-2" textColor="#aaa">
				Con los horarios actualizados del SIGA{" "}
				<Tooltip
					variant="outlined"
					title={`Última actualización el ${updated.toDate().toLocaleString(undefined, {
						//
						minute: "2-digit",
						hour: "2-digit",
						day: "numeric",
						weekday: "short",
						month: "short",
						year: "numeric",
					})}`}
				>
					<span className="font-bold text-[var(--secondary)]">
						{dayjs().to(dayjs(updated))} <InfoOutlined fontSize="small" />
					</span>
				</Tooltip>
				.
			</Typography>
		</Box>
	);
}
