import "./Home.css";
import IMAGE_ADD_HORARIO from "../../static/add-horario-option.png";
import IMAGE_LOAD_HORARIO from "../../static/load-horario-option.png";
import IMAGE_USM from "../../static/usm.png";
import IMAGE_USM_FOOTER from "../../static/three-colors.png";

import UpdatedBadge from "./components/UpdatedBadge";
import SedeLocator from "./components/SedeLocator";
import * as React from "react";
import { Button, ButtonPropsColorOverrides, Card, ColorPaletteProp, Grid, Box, Sheet, Stack, Typography, VariantProp, IconButton, Switch, Modal } from "@mui/joy";
import { SettingsContext } from "../../contexts/SettingsContext";
import { CalendarMonth, Close, DriveFolderUpload, EditCalendar } from "@mui/icons-material";
import Creator from "./components/Creator";
import Disclaimer from "./components/Disclaimer";
import { RamosContext } from "../../contexts/RamosContext";
import VersionLog from "./components/VersionLog";

export default function Home(props: { open: boolean; onClose: () => any; onOpenMalla: () => any }) {
	const [settings] = React.useContext(SettingsContext);
	const [ramos, setRamos] = React.useContext(RamosContext);

	const [optimized, setOptimized] = React.useState(Boolean(localStorage.getItem("optimized")));
	React.useEffect(() => {
		localStorage.setItem("optimized", "1");
		document.body.classList.toggle("optimized", optimized);
	}, [optimized]);

	const onNewHorario = () => {
		if (ramos?.length && !window.confirm("Ya hay ramos inscritos, ¿estás seguro de que quieres crear un nuevo horario?")) return;
		props.onClose();
		setRamos!([]);
	};

	const onLoadHorario = () => {
		if (ramos?.length && !window.confirm("Ya hay ramos inscritos, ¿estás seguro de que quieres cargar un horario?")) return;
		window.alert("Aún no está implementado!");
		//props.onClose();
	};

	if (!props.open) return <></>;
	return (
		<Box id="home-page">
			<Stack id="home-main-box" className="glass-bg" spacing={2}>
				<div id="home-main-box-bg" />
				<Stack className="w-full" direction="row-reverse" spacing={2}>
					<Button className="float-right" onClick={props.onClose} disabled={!settings?.sede || !ramos?.length} startDecorator={<CalendarMonth />} variant="outlined" color="neutral">
						Cerrar pestaña de inicio
					</Button>
				</Stack>
				<Grid container spacing={1} justifyContent="end">
					<HomeButton color="var(--secondary)" icon={<EditCalendar fontSize="small" />} label="Crear un nuevo horario" image={IMAGE_ADD_HORARIO} onClick={onNewHorario} />
					<HomeButton color="var(--terciary)" icon={<DriveFolderUpload fontSize="small" />} label="Cargar horario existente" image={IMAGE_LOAD_HORARIO} onClick={onLoadHorario} />
				</Grid>
				<SedeLocator />
				<MallaBanner onClick={props.onOpenMalla} />
				<Stack className="w-full" direction="row" justifyContent="space-between" alignItems="center" spacing={2}>
					<Typography component="label" endDecorator={<Switch checked={optimized} onChange={event => setOptimized(event.target.checked)} sx={{ ml: 1 }} />}>
						Optimizar página
					</Typography>
					<UpdatedBadge />
				</Stack>
				<img src={IMAGE_USM} className="absolute left-4 top-2 px-4 py-1 w-[20rem] pointer-events-none" />
			</Stack>
			<Creator />
			<Disclaimer />
			<VersionLog />
		</Box>
	);
}

function MallaBanner({ onClick }: { onClick: () => any }) {
	const [settings, setSettings] = React.useContext(SettingsContext);

	return (
		<Card className={`${!settings?.sede ? "opacity-40" : ""}`}>
			<Stack spacing={2}>
				<Stack className="w-1/2">
					<Typography>Malla Interactiva</Typography>
					<Typography level="body-sm">Visualiza la malla de tu carrera de manera interactiva e intuitiva.</Typography>
				</Stack>
				<Box className="w-flul flex flex-row-reverse">
					<Button variant="outlined" disabled={!settings?.sede} className="w-1/4" onClick={onClick}>
						Abrir malla interactiva
					</Button>
				</Box>
			</Stack>
		</Card>
	);
}

function HomeButton(props: { label: string; image?: string; color?: string; onClick?: () => any; icon?: React.ReactElement }) {
	const [settings] = React.useContext(SettingsContext);

	const disabled = !settings?.sede || !settings?.periodo;
	return (
		<Grid className="flex-1 aspect-[2]">
			<button //
				disabled={disabled}
				className={`w-full h-full relative overflow-hidden home-button ${disabled ? "opacity-40" : ""} rounded-xl border-[#fff4] border-[1px]`}
				onClick={props.onClick}
			>
				{!props.image || <img src={props.image} />}
				<div className="home-button-bg" />
				<Box className="absolute m-2 bottom-0 text">
					<Card size="sm">
						<Stack direction="row" alignItems="center" justifyContent="center">
							{props.icon || <></>}
							<Typography className="px-2">{props.label}</Typography>
						</Stack>
					</Card>
				</Box>
			</button>
		</Grid>
	);
}
