import { Add, AddComment } from "@mui/icons-material";
import { Button, Card, CardContent, CardOverflow, Divider, IconButton, Stack, Tooltip, Typography } from "@mui/joy";
import * as React from "react";
import { SettingsContext, ActionModes } from "../../../../contexts/SettingsContext";
import { RamosContext } from "../../../../contexts/RamosContext";
import Utils from "../../../../utils/utils";
import Color from "color";
import { NOMBRE_BACKGROUNDS, SIGLA_BACKGROUNDS } from "../../../../global/constants";
import Tier from "../Tier";
import { Ramo, RamoCarrera } from "../../../../typings/horario";
import SCT from "../SCT";
import { CARRERAS } from "../../../../global/data";
import Resume from "../Resume";

export default function ViewingAction() {
	const [settings, setSettings] = React.useContext(SettingsContext);

	return (
		<Stack justifyContent="space-between" className="w-full h-full">
			<Button
				startDecorator={<Add />}
				onClick={() => {
					setSettings!(s => ({ ...s, mode: ActionModes.ADDING }));
				}}
			>
				Añadir ramo/bloque
			</Button>
			<Stack spacing={2}>
				<Resume />
				<Divider />
				<RamosList />
			</Stack>
		</Stack>
	);
}

function RamosList() {
	const [ramos, setRamos] = React.useContext(RamosContext);
	const [settings, setSettings] = React.useContext(SettingsContext);

	const [highlighted, setHighlighted] = React.useState<Ramo | null>(null);
	React.useEffect(() => {
		if (highlighted) setRamos?.(r => [...r.map(ramo => (ramo.sigla === highlighted.sigla ? { ...ramo, highlighted: true } : ramo))]);
		else setRamos?.(r => [...r.map(ramo => ({ ...ramo, highlighted: false }))]);
		return () => setRamos?.(r => [...r.map(ramo => ({ ...ramo, highlighted: false }))]);
	}, [highlighted]);

	const creditosRamos = React.useMemo<number[]>(() => {
		let r: number[] = [];
		if (ramos)
			for (let i = 0; i < ramos.length; i++) {
				const [ramoCarrera, ramoPrograma] = Utils.getRamoData(ramos[i], settings?.sede);
				if (ramoCarrera?.creditos || ramoPrograma?.creditos) r.push((ramoCarrera?.creditos || ramoPrograma?.creditos)!);
			}
		return r;
	}, [ramos, settings?.sede]);

	return (
		<>
			<Card size="sm" variant="outlined">
				<CardOverflow variant="soft" sx={{ bgcolor: "black" }}>
					<CardContent orientation="horizontal" className="items-center justify-between h-full">
						<Typography level="body-sm">Ramos</Typography>
						{ramos?.length ? (
							<>
								<Typography level="body-xs" className="opacity-70">
									{ramos.length} ramo{ramos.length > 1 ? "s" : ""}
								</Typography>
								<SCT innacurate={creditosRamos.length !== ramos.length} creditos={creditosRamos.reduce((prev, curr) => prev + curr, 0)} />
							</>
						) : (
							<></>
						)}
					</CardContent>
					<Divider inset="context" />
				</CardOverflow>
				<Stack spacing={0.5}>
					{ramos?.length ? (
						ramos
							.sort((a, b) => a.sigla.localeCompare(b.sigla))
							.map(r => {
								let color = Color(Utils.ramoColor(r.sigla, r.nombre, r.color)).saturate(0.2).hexa();
								let background = SIGLA_BACKGROUNDS[r.sigla] ?? SIGLA_BACKGROUNDS[r.sigla.slice(0, 3)];
								if (!background) {
									let nombre = r.nombre.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
									for (const [str, value] of Object.entries(NOMBRE_BACKGROUNDS)) {
										if (nombre.match(str)) {
											background = value;
											break;
										}
									}
								}
								let textColor = Color(color).isLight() ? "#000" : "#fff";

								return (
									<Card //
										orientation="horizontal"
										sx={{
											background: `linear-gradient(to right, black 10%, white), linear-gradient(to right, ${color}, ${color}), ${background ? `url(${background})` : "linear-gradient(white, white)"}`,
											backgroundPosition: "right",
											backgroundBlendMode: "multiply",
										}}
										onMouseEnter={() => setHighlighted(r)}
										onMouseLeave={() => setHighlighted(null)}
										variant="outlined"
										size="sm"
										key={r.sigla}
										className="w-full flex justify-between items-center"
									>
										<CardOverflow
											variant="soft"
											sx={{
												//
												background: color,
												px: 1.5,
												justifyContent: "center",
												borderRight: "1px solid",
												borderColor: "divider",
												width: "50%",
											}}
										>
											<Stack className="absolute left-3 top-1/2 -translate-y-1/2" spacing={-1}>
												<Tooltip variant="outlined" followCursor title={r.nombre}>
													<Typography className="font-extrabold" sx={{ color: textColor }}>
														{r.sigla}
													</Typography>
												</Tooltip>
												<Typography level="body-sm">
													<Typography sx={{ color: textColor, opacity: 0.5 }}>PAR. </Typography>
													<Typography className="font-bold" sx={{ color: textColor }}>
														{r.paralelo}
													</Typography>
												</Typography>
											</Stack>
										</CardOverflow>
										<Stack direction="row" spacing={1}>
											<Button
												onClick={el => {
													setSettings!(s => ({ ...s, mode: ActionModes.EDITING }));
													setRamos!(ramos => [...ramos.sort((a, b) => (r.sigla === a.sigla ? -1 : 0))]);
												}}
												size="sm"
												variant="outlined"
												color="neutral"
												sx={{ background: "black" }}
											>
												Editar
											</Button>
											<Tooltip variant="outlined" title={`Dejar comentario para ${r.sigla}`}>
												<IconButton
													size="sm"
													color="primary"
													variant="outlined"
													onClick={() => {
														Utils.openInNewTab(
															`https://docs.google.com/forms/d/e/1FAIpQLScLul0z5_htUcC7YubsYKFszYQ9YhQkWOisG4UWH14p0w8Mrg/viewform?usp=pp_url&entry.1159151340=${encodeURIComponent(`${r.sigla}-${settings!.sede}-${settings!.jornada}-${settings!.periodo}`)}`
														);
													}}
												>
													<AddComment />
												</IconButton>
											</Tooltip>
										</Stack>
									</Card>
								);
							})
					) : (
						<Typography level="body-sm">No hay ningún ramo aún</Typography>
					)}
				</Stack>
			</Card>
		</>
	);
}
