import { Toll, Warning } from "@mui/icons-material";
import { Chip, Tooltip, Typography } from "@mui/joy";
import * as React from "react";
import { WARN_SCT } from "../../../global/constants";

export default function SCT({ sigla, creditos, innacurate }: { innacurate?: boolean; sigla?: string; creditos: number }) {
	return (
		<Tooltip
			variant="outlined"
			title={
				<Typography level="body-sm">
					{sigla ? `${sigla} posee ${innacurate ? "≥" : ""}${creditos} créditos SCT` : `Se tienen ${innacurate ? "≥" : ""}${creditos} créditos SCT en total`}.
					{innacurate ? (
						<>
							<br /> <Typography color="warning">Hay ramos a los cuales se les desconocen los créditos SCT.</Typography>
						</>
					) : (
						<></>
					)}
					{creditos >= WARN_SCT ? (
						<>
							<br />
							<Typography color="danger">La máxima cantidad de creditos a poder tomar es de {WARN_SCT} SCT.</Typography>
						</>
					) : (
						<></>
					)}
				</Typography>
			}
		>
			<Chip color={creditos >= WARN_SCT ? "danger" : innacurate ? "warning" : "neutral"} startDecorator={creditos >= WARN_SCT ? <Warning /> : <Toll />}>
				<Typography color={creditos >= WARN_SCT ? "danger" : innacurate ? "warning" : "neutral"} level="body-sm">
					{innacurate ? "≥" : ""}
					{creditos} SCT
				</Typography>
			</Chip>
		</Tooltip>
	);
}
