import * as React from "react";
import Home from "./pages/home/Home";
import * as dayjs from "dayjs";
import updateLocale from "dayjs/plugin/updateLocale";
import relativeTime from "dayjs/plugin/relativeTime";
import { RamosContext } from "./contexts/RamosContext";
import { ActionModes, Settings, SettingsContext } from "./contexts/SettingsContext";
import "dayjs/locale/es-mx";
import { BloqueIndividual, Ramo } from "./typings/horario";
import Calendar from "./pages/calendar/Calendar";
import Loader from "./pages/loader/Loader";
import { BloquesContext } from "./contexts/BloquesContext";
import MallaInteractiva, { SemestreMalla } from "./pages/home/MallaInteractiva";
import { MallaContext, MallaData } from "./contexts/MallaContext";

dayjs.extend(relativeTime);
dayjs.extend(updateLocale);
dayjs.locale("es-mx");

function App() {
	const [malla, setMalla] = React.useState<MallaData>({
		sede: localStorage.getItem("m-sede") ?? "",
		jornada: localStorage.getItem("m-jornada") ?? "",
		carrera: localStorage.getItem("m-carrera") ?? "",
		especialidad: Number.parseInt(localStorage.getItem("m-especialidad") ?? "-1"),
		plan: Number.parseInt(localStorage.getItem("m-plan") ?? "-1"),
	});
	const [settings, setSettings] = React.useState<Settings>({
		sede: localStorage.getItem("sede") ?? "",
		jornada: localStorage.getItem("jornada") ?? "",
		periodo: localStorage.getItem("periodo") ?? "",
		mode: ActionModes.VIEWING,
	});
	const [ramos, setRamos] = React.useState<Ramo[]>([]);
	const [bloquesI, setBloquesI] = React.useState<BloqueIndividual[]>([]);
	const [homeOpen, setHomeOpen] = React.useState(true);
	const [mallaOpen, setMallaOpen] = React.useState(false);

	React.useEffect(() => {
		if (settings?.sede) {
			localStorage.setItem("sede", settings.sede);
			if (!malla.sede) setMalla(m => ({ ...m, sede: settings.sede }));
		}
		if (settings?.jornada) {
			localStorage.setItem("jornada", settings.jornada);
			if (!malla.jornada) setMalla(m => ({ ...m, jornada: settings.jornada }));
		}
		if (settings?.periodo) localStorage.setItem("periodo", settings.periodo);
	}, [settings?.sede, settings?.jornada, settings?.periodo]);

	React.useEffect(() => {
		localStorage.setItem(
			"m-semestres",
			malla?.semestres
				?.flat()
				.filter(ramo => ramo.checked)
				.map(ramo => ramo.sigla)
				.join(";") ?? ""
		);
	}, [malla.semestres]);

	React.useEffect(() => {
		localStorage.setItem("m-jornada", malla.jornada);
		localStorage.setItem("m-sede", malla.sede);
		localStorage.setItem("m-plan", malla.plan.toString());
		localStorage.setItem("m-especialidad", malla.especialidad.toString());
		localStorage.setItem("m-carrera", malla.carrera);
	}, [malla.jornada, malla.sede, malla.plan, malla.especialidad, malla.carrera]);

	return (
		<>
			<SettingsContext.Provider value={[settings, setSettings]}>
				<MallaContext.Provider value={[malla, setMalla]}>
					<BloquesContext.Provider value={[bloquesI, setBloquesI]}>
						<RamosContext.Provider value={[ramos, setRamos]}>
							<Loader />
							<MallaInteractiva open={mallaOpen} onClose={() => setMallaOpen(false)} />
							<Home open={homeOpen} onOpenMalla={() => setMallaOpen(true)} onClose={() => setHomeOpen(false)} />
							<Calendar onHomeOpen={() => setHomeOpen(true)} />
						</RamosContext.Provider>
					</BloquesContext.Provider>
				</MallaContext.Provider>
			</SettingsContext.Provider>
		</>
	);
}

export default App;
