import { Box, Chip, Sheet, Stack, Tooltip, Typography } from "@mui/joy";
import * as React from "react";
import { Bloque, BloqueIndividual, BloqueItem, Ramo, RamoCarrera } from "../../../typings/horario";
import _ from "lodash";
import { NOMBRE_BACKGROUNDS, NOMBRE_COLORS, SIGLA_BACKGROUNDS, SIGLA_COLORS, SPECIAL_WORDS_SHORT } from "../../../global/constants";
import Color from "color";

import Utils from "../../../utils/utils";
import { LocationOn, People } from "@mui/icons-material";

type BloqueRamo = Bloque & { ramo: Ramo };

export default function Item(props: { current: (BloqueRamo | BloqueIndividual) & BloqueItem; right?: boolean; compact?: boolean; solo?: boolean }) {
	let { current, right, compact, solo } = props;
	let itemRamo = "ramo" in current;
	let currRamo = current as BloqueRamo & BloqueItem;
	let currBloque = current as BloqueIndividual & BloqueItem;

	const [nombreRamo, colorRamo] = React.useMemo(() => {
		const shorten = () => {
			if (!itemRamo) return (current as BloqueIndividual).nombre;

			let word = "";
			const special = ["CHILE", "RECIENTE", "EMPRESA", "I", "II", "III", "IV", "V", "VI", "VII", "VIII"];

			let nombre = currRamo.ramo.nombre //
				.normalize("NFD")
				.replace(/\b[\u0300-\u036f]\b/g, "")
				.replace(/\bfisica\b/gi, "FIS.")
				.replace(/\bmatematicas?\b/gi, "MAT.")
				.replace(/\binformatica\b/gi, "INF.")
				.replace(/\barquitectura\b/gi, "ARQUI.")
				.replace(/\bingenieria\b/gi, "ING.")
				.replace(/\bbiologia\b/gi, "BIO.")
				.replace(/\badministracion\b/gi, "ADMIN.")
				.replace(/\bintroduccion\b/gi, "INTRO.")
				.replace(/\borganizacion(?:es)?\b/gi, "ORG.")
				.replace(/\bprogramacion(?:es)?\b/gi, "PROGRA.")
				.replace(/\bespecial(?:es)?\b/gi, "ESP.")
				.replace(/\bmecanic(?:a|o)?\b/gi, "MEC.")
				.replace(/\btecnic(?:a|o)?\b/gi, "TEC.")
				.replace(/\bdiseños?\b/gi, "DIS.")
				.replace(/\bderechos?\b/gi, "DER.")
				.replace(/\burban(?:a|o)\b/gi, "URB.")
				.replace(/\bhabilidad(?:es)?\b/gi, "HAB.")
				.replace(/\bmanten(?:imiento|cion)?\b/gi, "MNT.")
				.replace(/\bseparacion\b/gi, "SEP.")
				.replace(/\bsimulacio(?:n|nes)\b/gi, "SIM.")
				.replace(/\b\bmachine learning\b/gi, "ML.")
				.replace(/\beducacion\b/gi, "EDUC.")
				.replace(/\bquimica\b/gi, "QUI.")
				.replace(/\bteoria\b/gi, "TEO.")
				.replace(/\bindustria\b/gi, "IND.")
				.replace(/\beconomia\b/gi, "ECO.")
				.replace(/\basistida\b/gi, "ASIS.")
				.replace(/\b\(.*?\)/gi, "");

			if (!solo || nombre.length > 20)
				nombre = nombre //
					.replace(/\bde la\b/gi, "")
					.replace(/\ba la\b/gi, "")
					.replace(/\bal\b/gi, "")
					.replace(/\ben\b/gi, "")
					.replace(/\bdel\b/gi, "")
					.replace(/\by\b/gi, "")
					.replace(/\bde\b/gi, "")
					.replace(/\bde\b/gi, "")
					.replace(/\bcon\b/gi, "")
					.replace(/\bpara(?: +el| +la)?\b/gi, "")
					.replace(/\bpor\b/gi, "");

			if (!solo) {
				nombre = nombre //
					.replace(/\btaller\b/gi, "T.")
					.replace(/\bcomputador(?:es)?\b/gi, "PC")
					.replace(/\bcomputacion\b/gi, "COMP.")
					.replace(/\btotal?\b/gi, "TOT.")
					.replace(/\bgeneral\b/gi, "GEN.")
					.replace(/\bprogra\.\b/gi, "PROG.")
					.replace(/\bteoric(?:a|o)\.\b/gi, "TEO.")
					.replace(/\baplicad(?:a|o)\.\b/gi, "APL.")
					.replace(/\barqui\.\b/gi, "ARQ.")
					.replace(/\bEDUC\.\b/gi, "EDU.")
					.replace(/\bhist\.\b/gi, "HIS.")
					.replace(/\bintro\.\b/gi, "INTR.");
				{
					let it = 0;
					let words = nombre.split(" ");
					while (nombre.length > 12 && it < words.length) {
						for (let i = 0; i < words.length; i++) {
							if (special.some(w => word.includes(w))) continue;
							word = words[i];
							if (word.length > 4) {
								nombre = nombre.replace(word, word.slice(0, 4) + ".");
								break;
							}
						}
						it++;
					}
				}

				// {
				// 	let splitted = nombre.split(" ");
				// 	for (let i = 0; i < splitted.length; i++) {
				// 		const word = splitted[i];
				// 		if (nombre.length < 10) break;
				// 		if (nombre.endsWith(".")) continue;
				// 		let short_l = SPECIAL_WORDS_SHORT[word] ?? 4;
				// 		if (nombre.replace(/\.$/g, "").length == short_l) continue;
				// 		short_l -= 1;
				// 		nombre = nombre.replace(word, word.slice(0, short_l + 1) + ".");
				// 	}
				// }
			}

			if (nombre.split(" ").length > 5 && nombre.length > (solo ? 28 : 20))
				nombre = nombre
					.split(" ")
					.filter(s => s)
					.map(word => (special.some(w => word.includes(w)) ? word : `${word[0]}.`))
					.join(" ");

			return nombre.replace(/ +/g, " ");
		};

		const color = () => {
			if (!itemRamo) return "";

			let color = Color(Utils.ramoColor(currRamo.ramo.sigla, currRamo.ramo.nombre, currRamo.ramo.color));
			color = color.darken(0.5);
			return color.hexa();
		};

		return [
			//
			shorten(),
			color(),
		];
	}, []);

	const tipoRamo = itemRamo ? currRamo.tipo : "";
	const highlightedRamo = itemRamo ? currRamo.ramo.highlighted : false;
	const salaRamo = itemRamo ? { SIN: "DESCONOCIDO" }[currRamo.sala ?? ""] ?? currRamo.sala : "";
	const siglaRamo = itemRamo ? currRamo.ramo.sigla : currBloque.nombre;

	const [backgroundImage, maskImage] =
		React.useMemo(() => {
			if (compact) return;
			if (!itemRamo) return ["", ""];
			let curr = current as BloqueRamo & BloqueItem;

			let background = SIGLA_BACKGROUNDS[curr.ramo.sigla] ?? SIGLA_BACKGROUNDS[curr.ramo.sigla.slice(0, 3)];
			if (!background) {
				let nombre = curr.ramo.nombre.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
				for (const [str, value] of Object.entries(NOMBRE_BACKGROUNDS)) {
					if (nombre.match(str)) {
						background = value;
						break;
					}
				}
			}

			let width: string | number = curr.tipo !== "Cát" ? 5 : 25;
			const edgeL = `${100 - width}%`;
			const edgeR = "100%";

			return [
				`${background ? `url(${background})` : "linear-gradient(#fff, #fff)"}, linear-gradient(to right, white, transparent), linear-gradient(${colorRamo}, ${colorRamo})`,
				`${
					{
						// Ayu: `url(${IMG_AYUDANTIA_OVERLAY})`,
						// Lab: `url(${IMG_LAB_OVERLAY})`,
						// Prá: `url(${IMG_PRATICA_OVERLAY})`,
					}[curr.tipo as string] ?? "linear-gradient(transparent, transparent)"
				}, linear-gradient(to right, transparent ${edgeL}, black ${edgeL}, black ${edgeR}, transparent ${edgeR})`,
			];
		}, []) ?? [];

	let titleColor = Color(colorRamo).lighten(1.2).rotate(5).saturate(0.5);

	return (
		<>
			<div
				className={`item-banner ${tipoRamo.toLowerCase()} ${solo ? "solo" : ""} ${compact ? "compact" : ""} ${!solo && !right ? "reversed" : ""}`}
				style={{
					backgroundImage,
					maskImage,
				}}
			/>
			<Box
				className={`item ${tipoRamo.toLowerCase()} ${highlightedRamo ? "highlighted" : ""} ${solo ? "solo" : ""} ${right ? "right" : ""} w-full h-full absolute left-0 top-0 p-2`}
				sx={{
					".item-title": {
						color: itemRamo ? (titleColor.isDark() ? titleColor.lighten(0.3).saturate(0.2) : titleColor).hex() : "white",
					},
				}}
			>
				<Stack className="w-full" {...(compact ? { direction: "row", justifyContent: "space-between", alignItems: "center" } : { spacing: -0.5 })}>
					<Box className="w-full h-full relative">
						<Typography //
							className="font-extrabold item-title"
							lineHeight={1.2}
							width={solo ? "100%" : "50%"}
							{...(right ? { textAlign: "right", right: 0, marginLeft: "auto" } : {})}
						>
							{nombreRamo}
						</Typography>
					</Box>
					<Stack className="w-full" direction="row" spacing={1} {...(right ? { justifyContent: "right" } : {})}>
						<Typography className="item-sigla" level="body-xs">
							{tipoRamo !== "Cát" && (
								<Typography className="item-tipo" level="body-xs">
									{solo
										? {
												Ayu: "AYUNDANTÍA",
												Prá: "PRÁCTICA",
												Lab: "LABORATORIO",
												Rec: "REC. CLASES",
												Otr: "OTRO",
										  }[tipoRamo as string]
										: tipoRamo.toUpperCase()}{" "}
								</Typography>
							)}
							{siglaRamo}
						</Typography>
						{compact ? (
							<Typography className="item-sala" level="body-xs" color="warning">
								{salaRamo}
							</Typography>
						) : (
							<Stack
								spacing={0.5}
								direction={!right ? "row" : "row-reverse"}
								flexWrap="wrap"
								className={`z-[5] p-1 absolute ${!right && !solo ? "left-0 px-0 origin-bottom-left" : "right-0 px-1 origin-bottom-right"} max-w-[${!solo ? "60%" : "100%"}] bottom-0 ${solo ? "scale-120" : "scale-90"}`}
							>
								<Tooltip size="sm" variant="outlined" title={currRamo.sala}>
									<Chip startDecorator={<LocationOn />} size="sm" color="warning">
										{salaRamo.replace(/-SJ\b/g, "")}
									</Chip>
								</Tooltip>
								{currRamo.profesor === "Sin profesor" || (
									<Tooltip size="sm" variant="outlined" title={currRamo.ramo.profesor.find(p => p.includes(currRamo.profesor.split(" ")[1]!)) ?? currRamo.ramo.profesor}>
										<Chip startDecorator={<People />} size="sm">
											{currRamo.profesor}
										</Chip>
									</Tooltip>
								)}
							</Stack>
						)}
					</Stack>
				</Stack>
			</Box>
		</>
	);
}
