import * as React from "react";

export enum ActionModes {
	VIEWING,
	ADDING,
	EDITING,
}

export interface Settings {
	sede: string;
	jornada: string;
	periodo: string;
	mode: ActionModes;
}

export const SettingsContext = React.createContext<[Settings?, React.Dispatch<React.SetStateAction<Settings>>?]>([]);
