import { ArrowUpward, Close } from "@mui/icons-material";
import { Box, Card, IconButton, Link, Typography } from "@mui/joy";
import * as React from "react";

export default function Dislaimer() {
	const [isHidden, setIsHidden] = React.useState(localStorage.getItem("disclaimer") ?? false);

	return (
		<Box className="absolute bottom-0 w-full overflow-hidden h-1/2 pointer-events-none">
			<Box className={`bottom-0 absolute bg-black w-full transition-transform duration-500 ${!isHidden ? "" : "translate-y-[80%]"}`}>
				<Box className="absolute h-full right-0 top-0 flex items-center z-10 mx-10">
					<Box className={`bg-black rounded-lg p-2 ${isHidden ? "-translate-y-[100%]" : ""} ease-linear transition-transform duration-200 pointer-events-auto`}>
						<IconButton
							variant="outlined"
							onClick={() => {
								setIsHidden(val => {
									if (val) localStorage.removeItem("disclaimer");
									else localStorage.setItem("disclaimer", "yes");
									return !val;
								});
							}}
						>
							{isHidden ? <ArrowUpward /> : <Close />}
						</IconButton>
					</Box>
				</Box>
				<Box className={`relative w-full flex justify-center items-center transition-opacity duration-500 ${!isHidden ? "" : "opacity-0"}`}>
					<Typography className="w-[60%] m-4 text-center p-2">
						<b>AVISO:</b> Esta página no está afiliada, asociada, autorizada, respaldada ni conectada de ninguna manera oficialmente con la{" "}
						<Link href="https://usm.cl/" textColor="var(--secondary)" className="pointer-events-auto">
							Universidad Técnica Federico Santa María
						</Link>{" "}
						o cualquiera de sus subsidiarias o afiliadas. La página no recopila información, ni utiliza cookies de terceros.
					</Typography>
				</Box>
			</Box>
		</Box>
	);
}
