import * as React from "react";
import { Card, Box, Avatar, Stack, Typography, Link, IconButton } from "@mui/joy";
import IMAGE_LUKKA from "../../../static/lukka.png";
import "./Creator.css";
import { CropLandscape, Instagram, MailOutlined, Minimize, Mood } from "@mui/icons-material";

const THRESHOLD = 15;
export default function Creator() {
	const [isHidden, setIsHidden] = React.useState(localStorage.getItem("author-minimized") ?? false);

	const creatorCardRef = React.useRef<HTMLDivElement>(null);
	const creatorCardShineRef = React.useRef<HTMLDivElement>(null);

	React.useEffect(() => {
		const onMouseMove = (e: MouseEvent) => {
			if (!creatorCardRef.current) return;
			const { clientX, clientY } = e;
			const width = window.outerWidth;
			const height = window.outerHeight;

			const horizontal = clientX / width;
			const vertical = clientY / height;

			const rotateX = horizontal * THRESHOLD - THRESHOLD / 2;
			const rotateY = vertical * THRESHOLD - THRESHOLD / 2;

			const card = creatorCardRef.current!;
			card.style.transform = `translate(${rotateX}px, ${rotateY}px) perspective(${card.clientWidth}px) rotateY(${rotateX}deg) rotateX(${rotateY}deg)`;
			card.style.setProperty("--gradient-x", `${(horizontal * 100 - 50) * 0.25}%`);
			card.style.setProperty("--particles-position", `${(horizontal * 100 - 50) * 0.5}% ${(vertical * 100 - 50) * 0.5}%`);
			card.style.setProperty("--shine-size", `${20 + (1 + Math.cos(horizontal * Math.PI * 2)) * 8 + (1 + Math.cos(vertical * Math.PI * 2)) * 8}rem`);

			const cardShine = creatorCardShineRef.current!;
			cardShine.style.left = `${horizontal * 100}%`;
			cardShine.style.top = `${vertical * 100}%`;
		};

		window.addEventListener("mousemove", onMouseMove);
		return () => window.removeEventListener("mousemove", onMouseMove);
	}, []);

	return (
		<Box className="absolute right-0 top-0 z-[10] m-2 min-w-[15rem] max-w-[15%]">
			<Box className="glass-bg rounded-lg">
				<Stack className="p-4">
					<Stack direction="row" justifyContent="space-between" alignItems="center">
						Contacto
						<IconButton
							size="sm"
							className="w-min float-right"
							variant="outlined"
							onClick={() => {
								setIsHidden(val => {
									if (val) localStorage.removeItem("author-minimized");
									else localStorage.setItem("author-minimized", "yes");
									return !val;
								});
							}}
						>
							{isHidden ? <CropLandscape /> : <Minimize />}
						</IconButton>
					</Stack>
					{isHidden ? (
						<></>
					) : (
						<Stack spacing={1}>
							<Box className="w-full flex justify-center items-center">
								<Box ref={creatorCardRef} className="rounded-xl px-2 mt-4 creator-card">
									<div ref={creatorCardShineRef} className="creator-card-shine" />
									<Stack spacing={1}>
										<Box component="div" className="creator-scrolling-text">
											CREADOR CREADOR CREADOR CREADOR CREADOR
										</Box>
										<img src={IMAGE_LUKKA} />
										<Box component="div" className="creator-scrolling-text">
											CREADOR CREADOR CREADOR CREADOR CREADOR
										</Box>
									</Stack>
								</Box>
							</Box>
							<Card size="sm" variant="outlined">
								<Stack spacing={1} className="pointer-events-auto z-10">
									<Link href="mailto:lenriquez@usm.cl" startDecorator={<MailOutlined />}>
										Correo de contacto
									</Link>
								</Stack>
							</Card>
							<Card size="sm" variant="outlined">
								<Stack spacing={1} className="pointer-events-auto z-10">
									<Link href="https://docs.google.com/forms/d/e/1FAIpQLSeKxJ4idy0vEZSqC_Ew5siparx6Lxy8kvP2ixWKBGc0Lwm6Jg/viewform?usp=dialog" startDecorator={<Mood />}>
										Sugerencias, comentarios y reclamos
									</Link>
								</Stack>
							</Card>
						</Stack>
					)}
				</Stack>
			</Box>
		</Box>
	);
}
