import { Box, Button, Card, Sheet, Stack, Tooltip, Typography } from "@mui/joy";
import * as React from "react";
import { RamosContext } from "../../contexts/RamosContext";
import "./Calendar.css";
import ActionBar from "./components/ActionBar";
import { ActionModes, SettingsContext } from "../../contexts/SettingsContext";
import _ from "lodash";
import Cell from "./components/Cell";
import { LocalDining } from "@mui/icons-material";
import { BloquesContext } from "../../contexts/BloquesContext";
import Utils from "../../utils/utils";
import { DIAS } from "../../global/constants";

export default function Calendar(props: { onHomeOpen: () => any }) {
	return (
		<Box id="calendar-page" className="flex">
			<ActionBar {...props} />
			<CalendarTable />
		</Box>
	);
}

function CalendarTable() {
	const [ramos] = React.useContext(RamosContext);
	const [bloquesI] = React.useContext(BloquesContext);

	const [minY, maxY] = React.useMemo(() => {
		let min = 0,
			max = 0;

		if (ramos) {
			max = _.maxBy(ramos.map(r => r.horario).flat(), b => b.bloque)?.bloque ?? 0;
			min = _.minBy(ramos.map(r => r.horario).flat(), b => b.bloque)?.bloque ?? 0;
		}

		return [min, max];
	}, [ramos]);

	const [minX, maxX] = React.useMemo(() => {
		let max = 0;
		if (ramos) max = _.maxBy(ramos.map(r => r.horario).flat(), b => b.dia)?.dia ?? 0;
		return [0, Math.max(max, 4)];
	}, [ramos]);

	return (
		<Box className="w-full h-full">
			<Stack direction="row" className="w-full h-full">
				{!(ramos?.length || bloquesI?.length) ? (
					<HorarioVacio />
				) : (
					<table className="w-full h-full">
						<tbody>
							<tr>
								<td className="w-[2%] h-0">
									<Sheet className="p-2 h-full w-full flex items-center justify-center overflow-hidden opacity-75">
										<Typography className="uppercase">Hora</Typography>
									</Sheet>
								</td>
								{DIAS.slice(minX, maxX + 1).map(day => (
									<td key={day}>
										<Sheet className="p-2 h-full w-full flex items-center justify-center overflow-hidden opacity-75">
											<Typography className="uppercase">{day}</Typography>
										</Sheet>
									</td>
								))}
							</tr>
							{_.range(minY - 1, maxY).map(i => {
								i++;
								return (
									<tr key={i}>
										<td className="w-0">
											<Sheet className="h-full w-full p-2 overflow-hidden relative">
												<Box className="h-full w-full overflow-hidden">
													<Typography className="absolute right-0 bottom-0 font-extrabold translate-y-[2rem] origin-bottom scale-y-[0.95] opacity-20 tracking-[-1rem] calendar-bloque" fontSize="6rem">
														{i}
													</Typography>
													<Typography>{Utils.bloqueToHora(i)}</Typography>
													{
														{
															9: (
																<Tooltip variant="outlined" title="Después de colación">
																	<Box className="absolute bottom-0 left-0 p-1.5">
																		<LocalDining color="warning" />
																	</Box>
																</Tooltip>
															),
														}[i]
													}
												</Box>
											</Sheet>
										</td>
										{DIAS.slice(minX, maxX + 1).map((_, day) => (
											<td className="max-w-[10%] w-full">
												<Cell key={day} bloqueIndex={i - 1} dayIndex={minX + day} />
											</td>
										))}
									</tr>
								);
							})}
						</tbody>
					</table>
				)}
			</Stack>
		</Box>
	);
}

function HorarioVacio() {
	const [settings] = React.useContext(SettingsContext);
	return (
		<Box id="calendar-blur" className="w-full h-full justify-center items-center flex">
			<Card className="w-1/2">
				<Typography level="title-lg">HORARIO VACÍO</Typography>
				<Box>
					<Typography level="body-sm">
						{
							{
								[ActionModes.VIEWING]: (
									<>
										En el menú de la izquierda puedes <Typography color="warning">ver, añadir y editar</Typography> los ramos inscritos en el horario, y en este espacio se <Typography color="warning"> va a representar el horario como tabla</Typography>. Para comenzar, empieza por
										añadir un ramo al horario a través del menú.
									</>
								),
								[ActionModes.ADDING]: (
									<>
										Escoge si vas a añadir un ramo o un bloque individual. <Typography color="warning">Los ramos son extraídos del SIGA, y tienen los horarios actualizados</Typography> (se puede comprobar en la pantalla de inicio). Después, completa los campos y elige los
										paralelos como se indique. Para buscar el ramo, puedes escribir la sigla en mayúsculas/mínúsculas, como también el nombre en sí del ramo. A la hora de elegir el ramo, puedes revisar sus créditos y horas semanales, como también obtener una vista previa en el
										horario que tienes actualmente al pasar el cursor por el paralelo respectivo.
									</>
								),
								[ActionModes.EDITING]: <></>,
							}[settings?.mode ?? ActionModes.VIEWING]
						}
					</Typography>
				</Box>
			</Card>
		</Box>
	);
}
