import { AspectRatio, Box, Button, ButtonProps, Card, CardContent, CardCover, CardOverflow, CardProps, Chip, ColorPaletteProp, Divider, IconButton, Link, List, ListItem, Modal, ModalClose, Option, Select, Sheet, Stack, Tooltip, Typography, TypographyPropsColorOverrides } from "@mui/joy";
import * as React from "react";
import { SettingsContext } from "../../contexts/SettingsContext";
import { ASIGNATURAS, CARRERAS } from "../../global/data";
import { Carrera, Planes, RamoCarrera, Semestre } from "../../typings/horario";
import { chunk, compact, difference, uniq } from "lodash";
import Utils from "../../utils/utils";
import Color from "color";
import SCT from "../calendar/components/SCT";
import { Article, Block, Check, Close, HelpOutline, HighlightTwoTone, InfoOutlined, Key, Lock, LockOpen, PictureAsPdf, Settings, Warning } from "@mui/icons-material";
import ListData from "../components/ListData";
import { MallaContext } from "../../contexts/MallaContext";

function romanize(num: number) {
	if (isNaN(num)) return NaN;
	let digits = String(+num).split(""),
		key = ["", "C", "CC", "CCC", "CD", "D", "DC", "DCC", "DCCC", "CM", "", "X", "XX", "XXX", "XL", "L", "LX", "LXX", "LXXX", "XC", "", "I", "II", "III", "IV", "V", "VI", "VII", "VIII", "IX"],
		roman = "",
		i = 3;
	while (i--) roman = (key[+digits.pop()! + i * 10] || "") + roman;
	return Array(+digits.join("") + 1).join("M") + roman;
}

const opcionesSede = Object.keys(ASIGNATURAS)
	.filter(str => str !== "date")
	.map(sede => (
		<Option key={sede} value={sede}>
			{sede}
		</Option>
	));

const ROW_SPACING = 0.2;

export interface RamoMalla extends RamoCarrera {
	sigla: string;
	locked?: boolean;
	checked?: boolean;
	unlocks?: string[][];
	unlockedBy?: string[][];
}
export type SemestreMalla = RamoMalla[];

export default function MallaInteractiva({ open, onClose }: { open: boolean; onClose: () => any }) {
	const [settings] = React.useContext(SettingsContext);
	const [malla, setMalla] = React.useContext(MallaContext);

	const carrera = React.useMemo(() => (!malla ? null : CARRERAS.find(c => c.código === malla?.carrera && c.sede === malla?.sede && c.jornada === malla?.jornada) ?? null), [malla?.sede, malla?.jornada, malla?.carrera, malla?.especialidad, malla?.plan]);

	React.useEffect(() => {
		if (!carrera || !malla) return;
		const especialidades = carrera?.["menciones/especialidades"];
		if (Object.keys(especialidades ?? {}).length > 0) setMalla!(m => ({ ...m, especialidad: 0 }));
	}, [carrera]);

	React.useEffect(() => {
		if (!carrera || !malla || malla.especialidad === -1) return;
		const planes = carrera?.["menciones/especialidades"]?.[malla.especialidad]?.planes;
		if (Object.keys(planes ?? {}).length > 0) setMalla!(m => ({ ...m, plan: 0 }));
	}, [carrera, malla?.especialidad]);

	const opcionesJornada = React.useMemo(
		() =>
			Object.keys(ASIGNATURAS[malla?.sede ?? ""] ?? {}).map(jornada => (
				<Option key={jornada} value={jornada}>
					{jornada}
				</Option>
			)),
		[malla?.sede]
	);

	const opcionesCarrera = React.useMemo(
		() =>
			CARRERAS.filter(c => c.sede === malla?.sede && c.jornada === malla?.jornada && Object.values(c["menciones/especialidades"]).some(mencion => Object.values(mencion.planes).length > 0)).map(carrera => (
				<Option key={carrera.código} value={carrera.código}>
					{carrera.nombre}
				</Option>
			)),
		[malla?.sede, malla?.jornada]
	);

	const opcionesMenciones = React.useMemo(
		() =>
			Object.entries(carrera?.["menciones/especialidades"] ?? {}).map(([especialidad, value], i) => (
				<Option key={especialidad} value={i}>
					{value.nombre}
				</Option>
			)),
		[carrera]
	);

	const opcionesPlanes = React.useMemo(
		() =>
			Object.entries(Object.values(carrera?.["menciones/especialidades"] ?? {})[malla?.especialidad ?? 0]?.planes ?? {}).map(([plan, value], i) => (
				<Option key={plan} value={i}>
					{value.plan}
				</Option>
			)),
		[carrera, malla?.especialidad]
	);

	React.useEffect(() => {
		if (!setMalla || !localStorage.getItem("m-carrera") || localStorage.getItem("m-carrera") !== malla?.carrera) return;
		const checkedRamos = (localStorage.getItem("m-semestres") ?? "").split(";");
		if (checkedRamos.length === 0) return;
		const timeout = setTimeout(() => {
			setMalla(m => ({
				...m,
				semestres: !m.semestres ? undefined : updateSemestres(m.semestres.map(semestre => semestre.map(ramo => (checkedRamos.includes(ramo.sigla) ? { ...ramo, checked: true } : ramo)))),
			}));
		}, 500);

		return () => {
			clearTimeout(timeout);
		};
	}, [setMalla]);

	React.useEffect(() => {
		setMalla?.(m => ({
			...m,
			semestres:
				malla && carrera && malla.especialidad !== -1 && malla.plan !== -1
					? [
							...Object.values(Object.values(carrera["menciones/especialidades"])[malla.especialidad].planes)
								[malla.plan]?.malla?.filter(semestre => Object.keys(semestre).length > 0)
								.map(semestre => Object.entries(semestre).map(([sigla, ramo]) => ({ ...ramo, sigla }))),
					  ]
					: undefined,
		}));
	}, [carrera, malla?.plan, malla?.especialidad]);

	const updateSemestres = (semestres: SemestreMalla[]) => {
		const checkedRamos = semestres.flat().filter(ramo => ramo.checked) ?? [];
		return [
			...semestres.map((semestre, _, semestres) =>
				semestre.map(ramo => {
					ramo.locked = false;
					if (ramo.checked || ramo.requisitos.length === 0) return ramo;
					if (
						checkedRamos.length > 0 &&
						!ramo.requisitos.every(opciones => {
							opciones = compact(
								opciones.filter(sigla =>
									semestres
										.flat()
										.map(ramo => ramo.sigla)
										.includes(sigla)
								)
							);
							return opciones.length === 0 || checkedRamos.some(r => opciones.includes(r.sigla));
						})
					)
						ramo.locked = true;

					return ramo;
				})
			),
		];
	};

	const timerRef = React.useRef<NodeJS.Timeout | null>(null);
	const isHoveredRef = React.useRef(false);
	const [hoveredRamo, setHoveredRamo] = React.useState<RamoMalla | null>(null);
	React.useEffect(() => {
		return () => clearTimeout(timerRef?.current ?? undefined);
	}, []);

	return (
		<Modal hideBackdrop={document.body.classList.contains("optimized")} aria-labelledby="modal-title" aria-describedby="modal-desc" open={open} onClose={onClose} sx={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
			<Sheet variant="outlined" className="glass-bg overflow-hidden" sx={{ background: "#0006", width: "95%", height: "95%", borderRadius: "md", p: 2, boxShadow: "lg" }}>
				<ModalClose variant="plain" sx={{ m: 1 }} />
				<Stack className="w-full h-full" spacing={1}>
					<Stack id="modal-title" className="pointer-events-auto" direction="row" spacing={2} alignItems="center" pr={10}>
						<Typography
							component="h2"
							level="h4"
							textColor="inherit"
							sx={{ fontWeight: "lg", mb: 1 }}
							endDecorator={
								<Tooltip
									className="z-10 pointer-events-auto"
									variant="outlined"
									title={
										<Box maxWidth={500}>
											<List marker="disc">
												<ListItem>Elige la sede, jornada, la carrera, y las respectivas menciones y plan de carrera (si es que tiene más de una que sea vigente).</ListItem>
												<ListItem>Haz click en un ramo de carrera para marcarlo como aprobado.</ListItem>
												<ListItem>Al marcar al menos un ramo como aprobado, automáticamente se ocultarán los ramos que no están desbloqueados.</ListItem>
												<ListItem>Si se deja el cursor sobre un ramo por un corto período de tiempo, se destacarán los ramos que se desbloquean al aprobar el ramo enfocado, y/o los ramos que desbloquean al ramo enfocado.</ListItem>
											</List>
										</Box>
									}
								>
									<Typography color="warning" level="body-sm" endDecorator={<HelpOutline />}>
										¿Cómo utilizar?
									</Typography>
								</Tooltip>
							}
						>
							Malla Interactiva
						</Typography>
						<Stack direction="row" spacing={1} alignItems="center">
							<Select
								value={malla?.sede}
								onChange={(event, value) => {
									value && setMalla?.(m => ({ ...m, sede: value ?? "", carrera: "", plan: -1, especialidad: -1 }));
								}}
							>
								{opcionesSede}
							</Select>
							<Select
								value={malla?.jornada}
								onChange={(event, value) => {
									value && setMalla?.(m => ({ ...m, jornada: value ?? "", carrera: "", plan: -1, especialidad: -1 }));
								}}
							>
								{opcionesJornada}
							</Select>
							<Select disabled={!malla?.sede || !malla?.jornada} value={malla?.carrera} onChange={(event, value: string | null) => value && setMalla?.(m => ({ ...m, carrera: value ?? "" }))} placeholder="Selecciona una carrera">
								{opcionesCarrera}
							</Select>
							<Select disabled={opcionesMenciones.length <= 1} value={Math.max(malla?.especialidad ?? 0, 0)} onChange={(event, value: number | null) => value !== null && setMalla?.(m => ({ ...m, especialidad: value }))} placeholder="Selecciona mención/especialidad">
								{opcionesMenciones}
							</Select>
							<Select disabled={opcionesPlanes.length <= 1} value={Math.max(malla?.plan ?? 0, 0)} onChange={(event, value: number | null) => value !== null && setMalla?.(m => ({ ...m, plan: value }))} placeholder="Selecciona el plan">
								{opcionesPlanes}
							</Select>
						</Stack>
					</Stack>
					<Stack className="h-full w-full" justifyContent="space-between" alignItems="center" direction="column">
						<Stack className={`w-full ${!malla?.semestres ? "opacity-70" : ""}`} direction="row" p={1} spacing={2}>
							<PercentCompletado semestres={malla?.semestres ?? []} />
							<RamoData title="Requisitos" data={hoveredRamo?.requisitos} color="primary" />
							<RamoData title="Equivalencias" data={hoveredRamo?.equivalencias} color="warning" />
						</Stack>
						<Card size="sm" className="relative h-full w-full overflow-hidden">
							{hoveredRamo && <Box className={`pointer-events-none absolute left-0 top-0 w-full h-full z-[5] bg-[#0008]`} />}
							{(malla?.semestres && (
								<Stack direction="row" className="overflow-x-auto pb-10 h-full" spacing={ROW_SPACING}>
									{malla?.semestres.map((_, i) => (
										<SemestreMalla
											onRamoClick={ramo => {
												setMalla?.(m => ({
													...m,
													semestres: !m.semestres
														? undefined
														: updateSemestres(
																m.semestres.map((semestre, index) =>
																	index === i
																		? [
																				...semestre.map(r =>
																					r.sigla === ramo.sigla
																						? {
																								...r,
																								checked: !r.checked,
																						  }
																						: r
																				),
																		  ]
																		: semestre
																)
														  ),
												}));
											}}
											onRamoHover={(ramo, enter) => {
												clearTimeout(timerRef?.current ?? undefined);

												if (enter)
													timerRef.current = setTimeout(() => {
														setMalla?.(m => ({
															...m,
															semestres: !m.semestres
																? undefined
																: [
																		...m.semestres.map(semestre =>
																			semestre.map(r => {
																				const unlocks: string[][] = r.requisitos;
																				const unlockedBy: string[][] = ramo.requisitos;
																				return {
																					...r,
																					unlocks: unlocks.some(and => and.some(or => or.includes(ramo.sigla))) ? unlocks : undefined,
																					unlockedBy: unlockedBy.some(and => and.some(or => or.includes(r.sigla))) ? unlockedBy : undefined,
																				};
																			})
																		),
																  ],
														}));
														isHoveredRef.current = true;
														setHoveredRamo(ramo);
													}, 300);
												else {
													if (isHoveredRef?.current) setMalla?.(m => ({ ...m, semestres: !m.semestres ? undefined : [...m.semestres.map(semestre => semestre.map(ramo => ({ ...ramo, unlockedBy: undefined, unlocks: undefined })))] }));
													isHoveredRef.current = false;
													setHoveredRamo(null);
												}
											}}
											key={i}
											semestre={malla?.semestres?.[i] ?? []}
											semestreIndex={i}
										/>
									))}
								</Stack>
							)) || (
								<Box className="w-full h-full flex justify-center items-center">
									<Typography level="title-md">
										Para visualizar la malla de una carrera, selecciona la <Typography color="warning">carrera, la mención/especialidad, y el código de plan</Typography>.
									</Typography>
								</Box>
							)}
						</Card>
					</Stack>
				</Stack>
			</Sheet>
		</Modal>
	);
}

function RamoData({ data, color, title }: { title: string; data?: string[][]; color: ColorPaletteProp & TypographyPropsColorOverrides }) {
	const ramos = React.useMemo(() => compact((data ?? []).filter(and => compact(and).length > 0)), [data]);
	const cardRef = React.useRef<HTMLDivElement | null>(null);

	return !data || !ramos.length ? (
		<></>
	) : (
		<Card ref={cardRef} size="sm" color={color} orientation="horizontal" className={`glass-bg`} sx={{ background: "#0008" }}>
			<CardOverflow variant="soft" sx={{ bgcolor: `${color}.softBg` }}>
				<Typography px={2} color={color} level="title-sm">
					{title.toUpperCase()}
				</Typography>
				<Divider orientation="vertical" inset="context" />
			</CardOverflow>
			<Stack className="overflow-hidden w-auto" px={1} direction="row" spacing={1} alignItems="center">
				{data.some(or => or.length >= 10) ? (
					<Typography noWrap level="body-sm">
						REVISAR EN PROGRAMA ACADÉMICO
					</Typography>
				) : (
					ramos.map(
						(and, i) =>
							and.join("/") && (
								<>
									{i > 0 ? (
										<Typography level="body-sm" color={color} key={`${i}+`}>
											+
										</Typography>
									) : (
										<></>
									)}
									<Typography level="body-sm" key={i}>
										{and.map((or, i, arr) => (
											<Typography key={i}>
												{or} {i < arr.length - 1 && arr.length > 1 ? <Typography color={color}>/ </Typography> : <></>}
											</Typography>
										))}
									</Typography>
								</>
							)
					)
				)}
			</Stack>
		</Card>
	);
}

function PercentCompletado({ semestres }: { semestres: SemestreMalla[] }) {
	const [totalRamos, checkedRamos] = React.useMemo(() => [semestres.flat(), semestres.flat().filter(r => r.checked)], [semestres]);
	const percentCompleted = Math.round((checkedRamos.length / totalRamos.length) * 100);

	if (!semestres.length) return <></>;
	return (
		<Stack spacing={2} direction="row" justifyContent="start" alignItems="center">
			<Tooltip
				variant="outlined"
				title={
					<ListData
						data={{
							"Ramos completados": {
								value: `*${checkedRamos.length}* / ${totalRamos.length}`,
							},
						}}
					/>
				}
			>
				<Card
					size="sm"
					className="flex justify-center items-center relative overflow-hidden"
					sx={{
						["&::after"]: {
							position: "absolute",
							left: 0,
							top: 0,
							transition: "width 0.4s ease-out",
							width: `${percentCompleted}%`,
							height: "100%",
							background: "var(--primary)",
							opacity: "80%",
							content: '""',
							mixBlendMode: "screen",
						},
					}}
				>
					<Typography level="body-xs" px={5} textColor="#fff" textAlign="center" endDecorator={<InfoOutlined />}>
						{percentCompleted}% DE CARRERA COMPLETADA
					</Typography>
				</Card>
			</Tooltip>
		</Stack>
	);
}

function SemestreMalla({ semestre, semestreIndex, onRamoClick, onRamoHover }: { semestre: SemestreMalla; semestreIndex: number; onRamoClick: (ramo: RamoMalla) => any; onRamoHover: (ramo: RamoMalla, enter: boolean) => any }) {
	const [creditosSCT, chunked] = React.useMemo(
		() => [
			//
			semestre.map(s => s.creditos).reduce((prev, curr) => prev + curr, 0),
			chunk(semestre, 6).map(s => s.sort((a, b) => (Utils.ramoColor(a.sigla, a.nombre) > Utils.ramoColor(b.sigla, b.nombre) ? 1 : -1))),
		],
		[semestre]
	);

	return (
		<Stack direction="row" spacing={ROW_SPACING}>
			<Stack direction="column" px={1}>
				<Stack p={0.5} direction="row" justifyContent="space-between" alignItems="center">
					{romanize(semestreIndex + 1)}{" "}
					<Typography textColor="text.primary" level="body-xs">
						{creditosSCT} SCT
					</Typography>
				</Stack>
				<Stack direction="row" spacing={0.5}>
					{chunked.map((s, i) => (
						<Stack key={i} direction="column" spacing={0.75}>
							{s.map(ramo => (
								<RamoMalla //
									key={ramo.sigla}
									ramo={ramo}
									onClick={() => onRamoClick(ramo)}
									onMouseOver={() => onRamoHover(ramo, true)}
									onMouseLeave={() => onRamoHover(ramo, false)}
								/>
							))}
						</Stack>
					))}
				</Stack>
			</Stack>
			<Divider orientation="vertical" />
		</Stack>
	);
}

function RamoMalla({ ramo, ...cardProps }: { ramo: RamoMalla } & CardProps) {
	const [fakeChecked, setFakeChecked] = React.useState(false);
	const [fakeUnlocked, setFakeUnlocked] = React.useState(false);

	const locked = React.useMemo(() => ramo.locked && !fakeUnlocked, [fakeUnlocked, ramo.locked]);
	const checked = React.useMemo(() => ramo.checked || fakeChecked, [fakeChecked, ramo.checked]);
	const bgColor = React.useMemo(() => Color(Utils.ramoColor(ramo.sigla, ramo.nombre)).darken(checked ? 1 : 0.2), [ramo]);
	const highlighted = ramo.unlockedBy || ramo.unlocks;

	return (
		<Card
			{...cardProps}
			size="sm"
			variant="outlined"
			color="neutral"
			className={`w-36 ${locked && !highlighted && !checked ? "opacity-40" : ""} select-none pointer-events-auto`}
			sx={{
				transition: "all 0.05s ease-out",
				["&:hover"]: { boxShadow: "md", borderColor: "neutral.outlinedHoverBorder", scale: 1.2, zIndex: 20 },
				[highlighted ? "&" : ""]: {
					scale: 1.1,
					zIndex: 10,
				},
			}}
			tabIndex={0}
			onKeyDown={ev => {
				if (ev.key.toLowerCase() === "c") setFakeChecked(curr => !curr);
				else if (ev.key.toLowerCase() === "a") setFakeUnlocked(curr => !curr);
			}}
		>
			<Box className="relative flex justify-center items-start w-full h-min aspect-[2] z-[4]">
				<Typography textOverflow="ellipsis" textColor={bgColor.isDark() ? "common.white" : "common.black"} textAlign="center" level="title-sm" fontSize={12}>
					{ramo.nombre}
				</Typography>
			</Box>
			<CardCover
				sx={{
					transition: "all .1s ease-out",
					background: `${bgColor.hex()} ${checked ? "repeating-linear-gradient(-45deg, #fff4 0, #fff4 20px, transparent 0, transparent 50%)" : ""}`,
					backgroundSize: "auto auto 80px 80px",
				}}
			/>
			<CardOverflow
				variant="soft"
				sx={{
					transition: "all .1s ease-out",
					bgcolor: ramo.unlocks ? "success.plainActiveBg" : ramo.unlockedBy ? "danger.plainActiveBg" : "background.level1",
				}}
				className="relative"
			>
				<Divider inset="context" />
				<CardContent orientation="horizontal" className="justify-between flex items-center">
					<Typography level="body-xs" textColor="text.secondary" sx={{ fontWeight: "md" }}>
						{ramo.sigla}
					</Typography>
					<Typography color="neutral" level="body-xs">
						{ramo.unlocks ? (
							<Typography color="success" level="body-xs" sx={{ fontWeight: "md" }} startDecorator={<Lock />}>
								{""}
							</Typography>
						) : ramo.unlockedBy ? (
							<Typography color="danger" level="body-xs" sx={{ fontWeight: "md" }} startDecorator={<Key />}>
								{""}
							</Typography>
						) : (
							<>{ramo.creditos} SCT</>
						)}
					</Typography>
				</CardContent>
			</CardOverflow>
		</Card>
	);
}
