import * as React from "react";
import "./Loader.css";
import SVG_LOGO from "../../static/logo-usm.svg";
import { Stack, Typography } from "@mui/joy";

export default function Loader() {
	const [open, setOpen] = React.useState(true);
	const backgroundRef = React.useRef<HTMLDivElement>(null);

	React.useEffect(() => {
		let closing = false;

		const load = async () => {
			closing = true;
			document.removeEventListener("DOMContentLoaded", load);
			if (!localStorage.getItem("sede")) await new Promise(r => setTimeout(r, 1000 + 600 * Math.random()));
			await new Promise(r => setTimeout(r, 500));
			backgroundRef.current!.style.opacity = "0";
			await new Promise(r => setTimeout(r, 500));
			setOpen(false);
		};

		if (document.readyState === "loading") {
			document.addEventListener("DOMContentLoaded", load);
			setTimeout(() => {
				if (closing) return;
				load();
			}, 2000);
		} else load();

		return () => document.removeEventListener("DOMContentLoaded", load);
	}, []);

	if (!open) return <></>;
	return (
		<div ref={backgroundRef} className="bg-white w-full h-full absolute left-0 top-0 m-0 flex justify-center items-center z-[1000] transition-all opacity-100 duration-[750ms] overflow-hidden">
			<Stack spacing={2} justifyContent="center" alignItems="center">
				<img src={SVG_LOGO} className="w-[18rem]" />
				<div id="usm-loader-ripples">
					<div className="usm-loader-ripple" />
					<div className="usm-loader-ripple delayed" />
				</div>
			</Stack>
		</div>
	);
}
