import { Box, Card, List, ListItem, Stack, Typography } from "@mui/joy";
import UPDATE from "../../../static/update.json";
import * as React from "react";
import dayjs from "dayjs";

const DETAILS = [
	//
	"Se arregló el problema de que solo se consideraba el primer requisito de un ramo en la malla interactiva, y no todos.",
	"Se pueden ver los grupos de requisitos de ramos que se necesitan para desbloquear un ramo, como es en el caso de ramos que necesitan más de un solo ramo como requisito.",
	"Se puede verificar en el selector de sede y jornada el período al cual corresponden los ramos obtenidos del SIGA.",
	"Actualización y mejora general de la malla interactiva. Se puede ver el porcentaje de ramos completados de la carrera, se guardan los parámetros escogidos, y también se almacena el estado de los ramos marcados.",
	"EXPERIMENTAL: ¿Quieres cooperar dejando tus conocimientos y recomendaciones de un ramo para que otros sepan? Ahora puedes, al tener un ramo elegido y darle al botoncito con el ícono de mensaje!",
];

export default function VersionLog() {
	return (
		<Box className="top-0 left-0 absolute m-2 max-w-[20%]">
			<Card>
				<Stack spacing={1}>
					<Stack spacing={-0.5}>
						<Typography level="h4">Historial de versión</Typography>
						<Typography level="body-xs">Página actualizada {dayjs().to(dayjs(new Date(UPDATE.date)))}</Typography>
					</Stack>
					<List marker="disc">
						{DETAILS.map((detail, i) => (
							<ListItem key={i}>
								<Typography level="body-sm">{detail}</Typography>
							</ListItem>
						))}
					</List>
				</Stack>
			</Card>
		</Box>
	);
}
